import React, {useContext, useState, useMemo, useRef, useCallback, useEffect} from 'react';
import {StoreContext} from "../../../context/store/storeContext";
import { AuthSignup } from '../../../utils/nanoit/auth';
import AddIcon from '@material-ui/icons/Add';
import axios from 'axios';
import { ControlPointSharp } from '@material-ui/icons';
import { useForm } from "react-hook-form";

function Signup() {
    const {state, actions} = useContext(StoreContext);

    const [profile, setProfile] = useState('');

    const clickGoToLogin = useCallback(() => {
        actions.viewActions.isHideSignup(true);
    },[]);


    //   
    const uploadProfile = (input) => {
        //     
        if(input.target.files && input.target.files[0]) {
            //  
            const fileUrl = window.URL.createObjectURL(new Blob([input.target.files[0]], { type: 'img' } ));
            const img = React.createElement('img', {src : fileUrl});
            
            setProfile(img);
            actions.viewActions.getProfileImg(input.target.files[0]);
        }else{
            setProfile('');
            actions.viewActions.getProfileImg('');
        }
    }

    const { register, handleSubmit, watch, formState: { errors } } = useForm();
    const onSubmit = data => {
        if( errors.name ){
            actions.viewActions.getToast('  ')
            return;
        }else{
            actions.generalActions.WebsocketSendData(AuthSignup(data.name, data.email, data.password));
        }
    }
    // console.log(watch('name'))


    return (
        <div className="signup">
            <div className="signup_inner">
                <h3></h3>
                <label className="add_propfile">
                    <input type="file" onChange={uploadProfile} />
                    <AddIcon className="icon_profile" />
                    {profile}
                </label>
                <p className="text_profile">  </p>

                <form onSubmit={handleSubmit(onSubmit)}>
                    <input type="text" placeholder="" className="input_common" {...register('name', {required:true})} />
                    <input autoComplete="username" type="email" placeholder="" className="input_common" {...register('email', {required:true})} />
                    <input autoComplete="current-password" type="password" placeholder="" className="input_common" {...register('password', {required:true})} />
                    <button className="btn_signup" type="submit" value="submit"></button>
                </form>
                
                <button className="btn_login" onClick={clickGoToLogin}> </button>
            </div>
        </div>
    );
}

export default Signup;