import { WebClient } from '@slack/web-api'

/************************************************************************
 * @name slackAPI
 * @summary slackAPI   (chat.postMessage)
 * @description API DOC : https://api.slack.com/methods/chat.postMessage
 ***********************************************************************/
const token = process.env.REACT_APP_SLACK_BOT_TOKEN;
const web = new WebClient(token);

export const sendMessageToSlack = async(error_message) => {
    const result = await web.chat.postMessage({
        text: error_message,
        channel: process.env.REACT_APP_CHANNEL_ID,
    });
    console.log(`   ${result.ts},  : ${error_message}`);
};

