import React, {useContext, useState, useEffect, useCallback} from "react";
import { getLocalItem, setLocalAddUserProfile } from "../../../context/localStorageActions";
import {StoreContext} from "../../../context/store/storeContext";
import { ProfileDownload } from "../../../utils/fileUtil";


function UserList() {
    const {state, actions} = useContext(StoreContext);
    const GET_LOCAL_FRIENDS_ALL = getLocalItem('NANOIT_CHAT_FRIENDS_ALL');


    useEffect(() => {
        state.generalStates.FRIENDS_ALL.map((user, i) => {
            /**
             * @summary file_id    store 
             * @description 
             * * state.generalStates.FRIENDS_ALL[?].proflie : {
             *      file_id:    id
             *      img:   img  .
             *   }
             *  
             *     (file_id  )    .
             */
            if( !!user.profile && !!user.profile.file_id && !user.profile.img){

                //  img ,  X
                const localUserImg = GET_LOCAL_FRIENDS_ALL.find(f=>f.id === user.id)?.profile?.img;
                if( localUserImg ) {
                    //console.log(' get profile from local ')
                    actions.generalActions.addProfileImg( user.profile.file_id, user.id, localUserImg);
                }else{
                    //console.log(' get profile from server ')
                    const callbackGetImg = img => {
                        actions.generalActions.addProfileImg( user.profile.file_id, user.id, img );
                        setLocalAddUserProfile(user, img);
                    }

                    ProfileDownload(
                        user.profile.file_id, 
                        state.generalStates.DEFAULT_CHAT_SERVER_URL,
                        state.generalStates.SESSION_JWT_TOKEN, 
                        callbackGetImg
                    );
                    console.log('profile images ')
                }
            }
        })
    }, [state.generalStates.FRIENDS_ALL]);


    return (
        <div className="roomlist userList">
            <ul>
                {
                    state.generalStates.FRIENDS_ALL.map((user, i) => 
                        <li key={i}>
                            <button>
                                <div className="left">
                                    {
                                        !!user.profile.img
                                        ? <img src={user.profile.img} />
                                        : <img src="images/noimg.jpg" alt="profile noimg"></img>
                                    }
                                </div>
        
                                <div className="right">
                                    <p>{user.auth.name}</p>
                                    <span>{user.auth.email}</span>
                                    {/* <p>{user.id}</p> */}
                                </div>
                            </button>
                        </li>
                    )
                }
            </ul>
        </div>
    );
}

export default React.memo(UserList);
