import React, {useCallback, useContext, useEffect, useRef, useState} from "react";
import ImageIcon from '@material-ui/icons/Image';
import SendIcon from '@material-ui/icons/Send';
import AttachFileIcon from '@material-ui/icons/AttachFile';
import SentimentSatisfiedOutlinedIcon from '@material-ui/icons/SentimentSatisfiedOutlined';
import Emoji from './Emoji';
import {MsgSend} from "../../../utils/nanoit/msg";
import {StoreContext} from "../../../context/store/storeContext";
import {FileSendAfterUpload} from "../../../utils/nanoit/file";
import axios from "axios";
import {TYPE_LOGGING_STATUS} from "../../../context/globals";
import SearchUsers from "./SearchUsers";
import ItemReply from "./ItemReply";
import ItemRelay from "./ItemRelay";
import { fileMimeChecker } from "../../../utils/fileUtil";
import { notifyRequestMentionCreate, notifyRequestReplyCreate } from "../../../utils/nanoit/notify";


function ChatSender() {
    const {state, actions} = useContext(StoreContext);

    const [viewEmoji, setViewEmoji] = useState(false);
    const [haveEmoji, setHaveEmoji] = useState('');
    const [message, setMessage] = useState("");
    const [showSearchUsers, setShowSearchUsers] = useState(false);

    const chatRef = useRef();
    const msgRef = useRef();

    /****************************************
     * useEffect
     ****************************************/
    // 
    useEffect(() => {
        const newMessage = msgRef.current.value.concat(haveEmoji);
        setMessage(newMessage);
        msgRef.current.value = msgRef.current.value + haveEmoji
    }, [haveEmoji]);

    // 
    useEffect(() => {
        const choice_user = state.viewStates.mentionUsers[state.viewStates.mentionUsers.length-1]?.name;
        const add_msg = !!choice_user ? `${choice_user} ` : '';
        const newMessage = msgRef.current.value.concat(add_msg);
        setMessage(newMessage);
    }, [state.viewStates.mentionUsers]);

    //   
    useEffect(() => {
        const { scrollHeight, clientHeight } = chatRef.current;
        chatRef.current.scrollTop = scrollHeight - clientHeight;
    }, [message]);


    /****************************************
     * start - mention
     ****************************************/
    const hideMentionPopup = () => {
        //     hide
        if( msgRef.current.value.indexOf('@') === -1 ){
            setShowSearchUsers(false)
        }
    }
    
    const showHideMentionPopup = (key) => {
        key === '@' ? setShowSearchUsers(true) : setShowSearchUsers(false);
    }

    /**
     * @name validateMentionUsers
     * @description
     * 1.    
     * -- @  
     * -- @       
     * --   store  (receiver )
     */
    const validateMentionUsers = useCallback(() => {
        if( message.indexOf('@') !== -1 ){
            const result_users = [];
            const topic = state.generalStates.TOPICS_MY.find(t => t.id === state.viewStates.selectopicid);
            const now_sequence_id = Number(topic.messages[topic.messages.length-1].sequence_id)+1;

            topic.users?.map((e, i) => {
                if (!e.auth?.name) return;
                const matching_users = message.split(' ').find(m => m === `@${e.auth.name}`);
                !!matching_users && result_users.push({id:e.id, name:e.auth.name});
            });
            /* actions.generalActions.setMentionInfo(
                state.viewStates.selectopicid,
                result_users,
                now_sequence_id
            ) */
            /* 
                actions.generalActions.WebsocketSendData(
                    notifyRequestMentionCreate(
                        state.viewStates.selectopicid,
                        result_users,
                        now_sequence_id
                    )
                )
            */
        }
    }, [message]);
    /****************************************
     * end - mention
     ****************************************/


    /****************************************
     * functions
    ****************************************/
    const onChangeMessage = useCallback((e) => {
        setMessage(e.target.value);
        hideMentionPopup();
    }, []);

    const onSend = useCallback(() => {
        if (!!message) {
            let msgType = '';
            let mentionUsersId = null;
            let replyUserId = null;
            let sequenceId = null;

            /**
             *  
             * 1. '@' .
             * 2. '@'      .
             * 
             * @name getMention
             * @param {reply-mention || metion || reply} type
             * @returns
             *  isMention   => 
             *  users_id    =>   
             */
            const getMention = (type) => {
                let isMention = false;
                let usersId = [];

                if( message.indexOf('@') !== -1 ) {
                    type==='mention' ? msgType='mention' : msgType='reply-mention';
                    isMention=true;

                    message.split(' ').map((content, i) => {
                        const getUser = state.generalStates.FRIENDS_ALL.find(m => `@${m.auth.name}` === content);
                        !!getUser && usersId.push(getUser);
                    })
                }
                return {isMention, usersId}
            }

            if( state.viewStates.popupMessageItems.reply === true ){
                const { isMention, usersId } = getMention('reply-mention');
                if(isMention) mentionUsersId=usersId;

                msgType = 'reply';
                replyUserId = state.viewStates.replyContent.from_user_id;
                sequenceId = state.viewStates.replyContent.sequence_id;

            }else{ //  
                const { isMention, usersId } = getMention('mention');
                if(isMention) mentionUsersId=usersId;
            }

            actions.generalActions.WebsocketSendData(
                MsgSend(
                    state.viewStates.selectopicid,
                    message,
                    msgType,
                    mentionUsersId,
                    replyUserId,
                    sequenceId,
                )
            )

            //  : REPLY  
            /* if( msgType==='reply' ){
                actions.generalActions.WebsocketSendData(
                    notifyRequestReplyCreate(
                        state.viewStates.selectopicid,
                        replyUserId,
                        sequenceId
                    )
                )
            }

            //  : MENTION  
            if( !!mentionUsersId ){
                actions.generalActions.WebsocketSendData(
                    notifyRequestMentionCreate(
                        state.viewStates.selectopicid,
                        mentionUsersId,
                        sequenceId
                    )
                )
            } */

            //    UI  
            actions.viewActions.isShowPopupMessageItems(false, 'reply'); 

            //actions.generalActions.WebsocketSendData(MsgSend(state.viewStates.selectopicid, message))

            validateMentionUsers();

            //  
            setMessage("")
            msgRef.current.value=""
            actions.viewActions.tempSaveMentionUsers('');
        }
    }, [message]);


    /**
     * @name onKeyDownSend
     * @summary input   
     * @param {*} e
     */
    const onKeyDownSend = (e) => {
        showHideMentionPopup(e.key); // 
        if (e.key === 'Enter') onSend(); //  
    }


    /**
     * @name uploadFile
     * @summary   &     
     * @param {*} e 
     */
    const uploadFile = (e) => {
        if(e.target.value=='') return;
        if( fileMimeChecker(e.target.files[0].name) ) {
            actions.viewActions.getToast('  .');
            return;
        }

        const topicId = state.viewStates.selectopicid;
        const url = state.generalStates.DEFAULT_CHAT_SERVER_URL + "/api/v1/upload/file?topic_id=" + topicId;
        const formData = new FormData();

        formData.append("file", e.target.files[0]);

        axios({
            method: 'POST',
            url: url,
            headers: {
                'Content-Type': 'multipart/form-data',
                'Authorization': 'Bearer ' + state.generalStates.SESSION_JWT_TOKEN
            },
            data: formData,
        })
        .then(function(response){ // handle success
            try {
                let data = response.data;
                let status = response.status;
                let statusText = response.statusText;
                let fileType = response.data.file.response.file.mime;
                let fileId = data.file.response.file.id;
                
                actions.generalActions.Logger(TYPE_LOGGING_STATUS, "[SEND] post file upload success, STATUS:" + status);
                actions.generalActions.Logger(TYPE_LOGGING_STATUS, "[SEND] post file upload success, STATUS_TEXT:" + statusText);
                actions.generalActions.Logger(TYPE_LOGGING_STATUS, "[SEND] post file upload success, DATA:" + JSON.stringify(data));
                actions.generalActions.WebsocketSendData(FileSendAfterUpload(topicId, fileId, fileType));
                actions.generalActions.WebsocketSendData(MsgSend(state.viewStates.selectopicid, String(fileType+":"+fileId) )); // msg
            } catch (error) {
                if (error.response) {
                    actions.generalActions.Logger(TYPE_LOGGING_STATUS, "[SEND] post file upload fail : " + error.response.status + ", " + error.response.statusText + ", " + JSON.stringify(error.response.data))
                } else if (error.request) {
                    actions.generalActions.Logger(TYPE_LOGGING_STATUS, "[SEND] post file upload fail, response receive fail")
                } else {
                    actions.generalActions.Logger(TYPE_LOGGING_STATUS, "[SEND] post file upload fail, catch fail1" + error.message)
                }
                actions.generalActions.Logger(TYPE_LOGGING_STATUS, "[SEND] post file upload fail, catch fail2" + error.config)
            }
        })
        .catch(function (error) { // handle error
            console.log('error======' + error);
        })

        e.target.value='';//  input
    }
    
    return (
        <div className="chatSender" ref={chatRef}>
            <button title="/ " className="btn_sendfile" onChange={uploadFile}>
                <form method="POST" className="uploadForm">
                    <label>
                        <AttachFileIcon />
                        <input type="file" className="file_sendfile" />
                    </label>
                </form>
            </button>
            {/* <button title=" " className="btn_sendimage" onChange={uploadFile}>
                <form method="POST" className="uploadForm">
                    <label>
                        <ImageIcon />
                        <input type="file" className="file_sendimage" />
                    </label>
                </form>
            </button> */}
            <button 
                title="" 
                className="btn_emoji"
                onClick={() => setViewEmoji( viewEmoji==true?false:true )}>
                <SentimentSatisfiedOutlinedIcon />
            </button>
            <div className="input_inner">
                <input
                    type="text" 
                    className="input_send_msg" 
                    placeholder=" " 
                    onChange={onChangeMessage}
                    onKeyDown={onKeyDownSend}
                    value={message}
                    ref={msgRef}
                />
            </div>

            <button title=" " className="btn_sendmsg" onClick={onSend}>
                <SendIcon className={ msgRef?.current?.value ? 'icon_send_msg active' : 'icon_send_msg'} />
            </button>

            <Emoji viewEmoji={viewEmoji} setViewEmoji={setViewEmoji} setHaveEmoji={setHaveEmoji} />

            {
                showSearchUsers === true &&
                <SearchUsers 
                    msgRef={msgRef}
                    setShowSearchUsers={setShowSearchUsers} />
            }

            {   /*  UI */
                state.viewStates.popupMessageItems.reply === true &&
                <ItemReply />
            }

            {   /*  UI */
                state.viewStates.popupMessageItems.relay === true &&
                <ItemRelay />
            }
        </div>
    );
}

export default React.memo(ChatSender);