import React, {useContext, useState, useEffect, useRef} from "react";
import {CtrlTopicCreate} from "../../../utils/nanoit/ctrl";
import {StoreContext} from "../../../context/store/storeContext";
import CloseIcon from '@material-ui/icons/Close';
import {MetaRequestTopicListMe} from "../../../utils/nanoit/meta";

function CreateTopic({ setViewType }) {
    const {state, actions} = useContext(StoreContext)

    const [topicName, setTopicName] = useState('')
    const [usePrivate, setUsePrivate] = useState(false)
    const inputRef = useRef();

    const handleTopicName = (e) => {
        setTopicName(e.target.value)
    }

    const handleUsePrivate = (e) => {
        setUsePrivate(e.target.checked)
    }

    const handleOnClickCreate = () => {
        actions.generalActions.WebsocketSendData(CtrlTopicCreate(topicName, "alone", usePrivate)) //   
        //actions.generalActions.WebsocketSendData(MetaRequestTopicListMe()); //      !
        alert(topicName + '   ');

        /*  */
        actions.viewActions.isShowChatMobile(true);   //   UI 
        actions.viewActions.isShowCreateTopic(false); //  UI 
        setViewType("MyList");     //  
        setTopicName('');          //    ()
        inputRef.current.value=""; //    ()
    }

    return (
        <div className="createTopic" isshow={state.viewStates.isShowCreateTopic===true ? 'active' : ''}>
            <div>
                <div className="input_wrap">
                    <input type="text" onChange={handleTopicName} valuecheck={topicName != '' ? 'active' : ''} ref={inputRef} />
                    <p>  </p>
                </div>

                <label >
                    <input type="checkbox" name="checkedA" checked={usePrivate} onChange={handleUsePrivate} />
                    <span>  </span>
                </label>

                <button className="btn_create" onClick={handleOnClickCreate}> </button>

                <button 
                    className="btn_close_create"
                    onClick={() => actions.viewActions.isShowCreateTopic(false)}>
                    <CloseIcon />
                </button>
            </div>
        </div>
    );
}

export default React.memo(CreateTopic);
