import React, {useContext, useState ,useEffect, useRef, useCallback} from 'react';
import {StoreContext} from "../../../context/store/storeContext";
import axios from "axios";
import GetAppIcon from '@material-ui/icons/GetApp';
import PopupMessage from './PopupMessage';
import SubdirectoryArrowRightIcon from '@material-ui/icons/SubdirectoryArrowRight';
import { fileUrlCreater } from '../../../utils/fileUtil';
import { getLocalUserTopic } from '../../../context/localStorageActions';


function ChatReciever() {
    const {state, actions} = useContext(StoreContext);

    const [msgAll, setMsgAll] = useState('');
    const [isShowPopupMessage, setIsShowPopupMessage] = useState(false);
    const [getClickMessage, setGetClickMessage] = useState(''); //    
    const [whoIsClickMsg, setWhoIsClickMsg] = useState('right');

    const chatRef = useRef();

    const userId = state.generalStates.SESSION_USER_INFO.id;
    const topicId = state.viewStates.selectopicid;

    const localUserTopic = getLocalUserTopic(userId, topicId);

    const openTopic = state.generalStates.TOPICS_MY.filter(e => e.id === topicId)[0];


    /****************************************
        functions
    ****************************************/
    /**
     * @name actionForPopup
     * @summary      
     * @description
     * * 1.     show
     * * 2.  msg 
     * @param {msg}    
     */
    const actionForPopup = useCallback((msg, who) => {
        setGetClickMessage(msg);        //    
        setIsShowPopupMessage(true);    // show  UI
        actions.viewActions.isShowPopupMessageItems(false, 'reply'); //   '' UI  

        if( who === 'left' ) {
            setWhoIsClickMsg('left'); //  &     

            // reply  sender    store 
            actions.viewActions.getReplyContent({
                from_user_id:msg.from_user_id,
                from_user_name:msg.from_user_name,
                content:msg.content,
                sequence_id:msg.sequence_id
            });
        }
        else setWhoIsClickMsg('right');
    }, []);


    /**
     * @name changeBlobToFile
     * @summary id   
     */
    const changeBlobToFile = (idx, fileId, fileType) => {
        if(!idx || !fileId || !fileType) return;
        if( !!openTopic.messages[idx].file ) return;
        // const local_path = 'C:\Users\nanoit\Desktop'; //     ...

        try{
            return axios({
                method:'GET',
                url:state.generalStates.DEFAULT_CHAT_SERVER_URL + '/api/v1/download/file?file_id=' + fileId,
                responseType: fileType==='image' ? 'blob' : 'arraybuffer',
                headers:{
                    'Authorization': 'Bearer ' + state.generalStates.SESSION_JWT_TOKEN
                },
            });
        }catch(error){
            console.log(error)
        }
    }


    let timer; // timer  value      
    /**
     * @name showMesssagePopup
     * @summary  n    
     */
    const showMesssagePopup = (msg, who, event) => {
        if( event === 'mousedown' ){
            timer = setTimeout(() => actionForPopup(msg, who) ,1000);
        }
        else clearTimeout(timer);
    }


    /**
     * @name actionToFindMessage
     * @summary     
     */
    const actionToFindMessage = useCallback((reply_id) => {
        const getMessageDOM = document.getElementById(reply_id);
        getMessageDOM.classList.add('active');
        setTimeout(() => getMessageDOM.classList.remove('active'), 1000);
    },[]);


    /**
     * @name getReplyInformation
     * @summary    
     */
    const getReplyInformation = useCallback((msg, messages) => {
        let is_reply = false;
        let reply_message = '';
        let reply_id = '';

        if( !!msg?.custom?.notify.reply?.sequence_id ){
            is_reply = true;
            reply_message = messages?.find(m => m.sequence_id === msg?.custom?.notify.reply.sequence_id);
        }

        //  undefined  
        if( !reply_message ) return <p></p>;
        reply_id = `reply_${reply_message.topic_id}_${reply_message.sequence_id}`;

        return {reply_message, reply_id, is_reply}
    },[]);


    /**
     * @name getMentionInformation
     * @summary    
     * @description  :        
     * *  
     * * 1.     
     */

    /*
    const getMentionInformation = (msg) => {
        let isMention = false;
        let myMentionMsg = null;

        if( !!msg?.custom?.notify.mention ){
            isMention = true;
            const myMention = msg.custom.notify.mention.to_users_id.map((user, i) => !!user.find(u => u.id === state.generalStates.SESSION_USER_INFO.id))
            myMentionMsg = myMention; 
        }
        return { isMention, myMentionMsg }
    }
    */


    /**
     * @name updateFileByMessage
     * @summary     / 
        //    https://developer.mozilla.org/ko/docs/Web/HTTP/Basics_of_HTTP/MIME_types/Common_types
     * 
     */
    const updateFileByMessage = async() => {
        //  for    .
        const roomMessages = openTopic?.messages;
        for( let i=0; i < roomMessages?.length; i++ ){
            const fileId = roomMessages[i].content.split(':')[1];
            const fileType = roomMessages[i].content?.split('/')[0];

            if( roomMessages[i]?.file ) continue; //  
            if( !fileId && !fileType ) continue;  //  

            if( localUserTopic.length && localUserTopic.messages[i]?.file ){ //   
                roomMessages[i].file = localUserTopic.messages[i].file;
                console.log('   store ');
                continue;
            }else{ //    
                
                roomMessages[i].file = ''; // file key  (  file  )
                if( fileType === "image" ) {
                    const getImage = await changeBlobToFile(i, fileId, "image");
                    const reader = new FileReader();
                    reader.readAsDataURL(getImage.data);
                    reader.onloadend = () => roomMessages[i].file = reader.result;
                }else if( fileType === "application" || fileType === "text" ){
                    const getFile = await changeBlobToFile(i, fileId, "other");
                    const url = fileUrlCreater(getFile, roomMessages[i].content);
                    roomMessages[i].file = url;
                }
            }
        }

        /**
         *     await         .
         * setTimeout 
         */
        setTimeout(() => {
            setMsgAll(openTopic);
        }, 10);
    };

    /**
     * @name moveScrollToBottom
     * @summary    
     */
    const moveScrollToBottom = useCallback(() => {
        setTimeout(() => {
            const { scrollHeight, clientHeight } = chatRef.current;
            chatRef.current.scrollTop = scrollHeight - clientHeight;
        }, 1);
    }, []);


    /**
     * @name msgNotifyCatcher
     * @summary   reply, mention     
     */
    /*
    const msgNotifyCatcher = (msg) => {
        if( msg.custom.notify === 'reply' ){
            console.log('msgNotifyCatcher');
            actions.generalActions.WebsocketSendData(
                notifyRequestReplyCreate(
                    state.viewStates.selectopicid,
                    msg.from_user_id,
                    msg.sequence_id
                )
            )
        }
    }
    */


    /****************************************
        useEffect
    ****************************************/
    //        
    useEffect(() => {
        if( openTopic?.messages?.find(m => !!m.content.split(':')[1] || !!m.content.split('/')[0]) ){
            updateFileByMessage();
        }
    }, [state.viewStates.selectopicid]);


    //     +   
    useEffect(() => {
        // setLocalUpdateMessage(userId, topicId, openTopic?.messages);
        moveScrollToBottom();
    }, [msgAll, openTopic]);


    /****************************************
        start - msg component
    ****************************************/
    const MsgFile = ({ msg }) => {
        const fileType = msg.content.split('/')[0];
        const fileId = msg.content.split(':')[1];

        return(
            fileType === 'image'
            ? <img src={msg.file} />
            : <div>
                <a 
                    href={msg.file}
                    className="btn_filedownload" 
                    download={`file_${fileId}`}
                >
                    <div className="filedown_text">
                        <p> </p>
                        {`file_${fileId}`}
                    </div>
                    <GetAppIcon className="icon_download" />
                </a>
            </div>
        )
    }


    const MsgMention = useCallback(({ msg }) => {
        return(
            msg.content.split(' ').map((content, i) => {
                if( !!state.generalStates.FRIENDS_ALL.find(m => `@${m.auth.name}` === content) ) {
                    return <button key={i}>{content}</button>
                }
                else return content
            })
        )
    },[state.generalStates.FRIENDS_ALL]);


    // user
    const MsgLeft = useCallback(({ msg, messages }) => {
        const { reply_message, reply_id, is_reply } = getReplyInformation(msg, messages);
        // const { isMention, myMentionMsg } = getMentionInformation(msg);

        //   
        let img = state.generalStates.FRIENDS_ALL.find(e => e.id === msg.from_user_id);
        img = !!img && img.profile.img;


        return (
            <li className="msg_outer left user" id={`reply_${msg.topic_id}_${msg.sequence_id}`}>
                <div className="msg_left_profile">
                    { !!img ? <img src={img} /> : <img src="images/noimg.jpg" alt="profile noimg" /> }
                </div>
                <div className="msg_left_userinfo">
                    <div className="top_name">
                        {msg.from_user_name}
                    </div>
                    <div className="btm_msg">
                        <div className="msg_inner"
                            onClick={() => !!reply_message && actionToFindMessage(reply_id)}
                            onMouseUp={() => showMesssagePopup(msg, 'left', 'mouseup')}
                            onMouseDown={() => showMesssagePopup(msg, 'left', 'mousedown')}
                        >
                            {
                                is_reply === true &&
                                <a className="msg_reply"
                                    href={`#${reply_id}`}
                                >
                                    {reply_message.from_user_name} <br />
                                    {reply_message.reply_content}
                                </a>
                            }
                            {
                                <div className="msg">
                                    { is_reply === true && <SubdirectoryArrowRightIcon className="icon_reply" /> }

                                    {   // 1 
                                        msg.content.indexOf('@') === -1  && !msg.file
                                        ? msg.content
                                        : ( // 2 
                                            msg.content.indexOf('@') !== -1
                                        )?( 
                                            <MsgMention msg={msg} />
                                        ):( // !!msg.file
                                            <MsgFile msg={msg} />
                                        )
                                    }
                                </div>
                            }
                        </div>
                    </div>
                </div>
            </li>
        )
    }, [state.generalStates.FRIENDS_ALL, openTopic]); //   


    // info
    const MsgCenter = useCallback(({ msg }) => {

        return(
            <li className="msg_outer center">
                <div className="msg_inner">
                    <div className="msg">{msg.content}</div>
                </div>
            </li>
        )
    } , []);


    // me
    const MsgRight = useCallback(({ msg, messages }) => {
        const { reply_message, reply_id, is_reply } = getReplyInformation(msg, messages);


        return (
            <li className="msg_outer right me" id={`reply_${msg.topic_id}_${msg.sequence_id}`}>
                <div className="msg_inner"
                    onClick={() => !!reply_message && actionToFindMessage(reply_id)}
                    onMouseDown={() => showMesssagePopup(msg, 'right', 'mousedown')}
                    onMouseUp={() => showMesssagePopup(msg, 'right', 'mouseup')}
                >
                    {
                        is_reply === true &&
                        <a className="msg_reply"
                            href={`#${reply_id}`}
                        >
                            {reply_message.from_user_name} <br />
                            {reply_message.content}
                        </a>
                    }
                    <div className="msg">
                        { is_reply === true && <SubdirectoryArrowRightIcon className="icon_reply" /> }
                        {   // 1 
                            msg.content.indexOf('@') === -1  && !msg.file
                            ? msg.content
                            : ( // 2 
                                msg.content.indexOf('@') !== -1
                            )?(
                                <MsgMention msg={msg} />
                            ):( // !!msg.file
                                <MsgFile msg={msg} />
                            )
                        }
                    </div>
                </div>
            </li>
        )
    }, [state.generalStates.FRIENDS_ALL]); //   

    /****************************************
        end - msg component
    ****************************************/

    return (
        <>
        <div className="chatReciever" ref={chatRef} isshow={state.viewStates.isChatboxLoading===true ? "active" : ""}>
            <ul>
                {
                    openTopic?.messages?.map((msg, i) => 
                        msg.message_type == 'ctrl' ?
                        (
                            <MsgCenter key={i} msg={msg} />
                        ) : (
                            !!msg.content && (
                                msg.from_user_id === userId
                                ? <MsgRight key={i} msg={msg} messages={openTopic.messages} />
                                : <MsgLeft key={i} msg={msg} messages={openTopic.messages} />
                            )
                        )
                    )
                }
            </ul>
        </div>

        {isShowPopupMessage === true &&
            <PopupMessage
                setIsShowPopupMessage={setIsShowPopupMessage}
                getClickMessage={getClickMessage}
                whoIsClickMsg={whoIsClickMsg}
            />
        }
        </>
    );
}

export default React.memo(ChatReciever);