import React from 'react';
import ReactDOM from 'react-dom';
import App from './app';
import {StoreProvider} from "./context/store/storeContext";
import ReactCatcher from 'react-catcher';
import { sendMessageToSlack } from './utils/errorlog';

ReactDOM.render(
    /**
     * componentDidCatch   
     * @name ReactCatcher: https://github.com/ibrahim-13/react-catch
     * @name componentDidCatch: https://react.vlpt.us/basic/26-componentDidCatch-and-sentry.html
     * 
     *    
     * 1.  slack message - 
     * 2.      
     */
    <ReactCatcher
        fallback={<h1>  ...     :( </h1>}
        errorHandler={(error, errorInfo) => {
            const error_message = `error: ${error}`;
            console.log(error_message)
            // errorInfo: ${JSON.stringify(errorInfo)}
            // sendMessageToSlack(error_message);
        }}
    >
        <StoreProvider>
            <App/>
        </StoreProvider>
    </ReactCatcher>,
    document.getElementById('root')
);