import React, { useContext, useEffect, useRef, useState } from 'react';
import ReplyIcon from '@material-ui/icons/Reply';
import {StoreContext} from "../../../context/store/storeContext";
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import ChatIcon from '@material-ui/icons/Chat';
import { MsgSend } from '../../../utils/nanoit/msg';
import { FileSendAfterUpload } from '../../../utils/nanoit/file'
import { getLocalItem } from '../../../context/localStorageActions';

/**
 * @name ItemRelay 
 * @summary  (/)   
 * @description 
 * *      .   1:1     
 */
function ItemRelay() {
    const {state, actions} = useContext(StoreContext);
    const [activeTab, setActiveTab] = useState('topic');
    const inputRef = useRef([]);

    //  
    const sendToUser = () => {
        alert(' ')
    }

    //   
    const sendToTopic = () => {
        /**
         * 1. 
         * 2.   
         * 3. ''  
         * 4.  UI  
         * 
         * error 
         * 1.     .
         * 2.   fileid, fileType ,     .
         */
        //   
        const relay_to_topicid = Number(inputRef.current.find(input => input.checked === true)?.value);
        if( !relay_to_topicid ) return;
        actions.viewActions.selecTopic(relay_to_topicid); // 1
        /*
        //   blob
        const GET_LOCAL_NANOIT = getLocalItem('NANOIT');
        if( !GET_LOCAL_NANOIT ) return;
        const user = GET_LOCAL_NANOIT.CHAT.USERS.find(u => u.id === state.generalStates.SESSION_USER_INFO.id);
        */
        const topic = state.generalStates.TOPICS_MY.find(t => t.id === state.viewStates.msgForRelay.topic_id);
        const msg = topic?.messages?.filter(m =>m.sequence_id === state.viewStates.msgForRelay.sequence_id);

        const fileId = msg[0].content.split(':')[1];
        const fileType = msg[0].content.split(':')[0];

        actions.generalActions.WebsocketSendData(FileSendAfterUpload(state.viewStates.msgForRelay.topic_id, fileId,fileType)); 
        actions.generalActions.WebsocketSendData(MsgSend(state.viewStates.msgForRelay.topic_id, String(fileType+":"+fileId) )); // 2
        actions.viewActions.getToast(' ') // 3
        actions.viewActions.isShowPopupMessageItems(false, 'relay'); // 4
    }


    // 
    const clickSubmit = () => {
        if( activeTab === 'user' ){
            // sendToUser()
        }else if( activeTab === 'topic' ){
            sendToTopic()
        }
    }

    // : UI 
    const clickCancel = () => {
        actions.viewActions.isShowPopupMessageItems(false, 'relay');
    }

    return (
        <div className="ItemRelay">
            <div className="top">
                <span></span>
            </div>
            <div className="mid">

                <div className="tab">
                    {/* <button 
                        className={activeTab === 'user' ? 'active' : ''} 
                        onClick={() => setActiveTab('user')}>
                            
                    </button> */}

                    <button 
                        className={activeTab === 'topic' ? 'active' : ''} 
                        onClick={() => setActiveTab('topic')}
                        >
                             
                    </button>
                </div>

                <div className="relaylist">
                    <ul>
                        {/*  */
                            /* activeTab === 'user' &&
                            state.generalStates.FRIENDS_ALL.map((user, i) =>
                                <li key={i}>
                                    <label>
                                        {   user.profile.img !== undefined
                                            ? user.profile.img
                                            : <img src="images/noimg.jpg" alt="profile noimg" />
                                        }
                                        <p className="name">{user.auth.name}</p>
                                        <input type="radio" name="input_relay" />
                                        <CheckBoxOutlineBlankIcon className="checkicon icon_unchecked" />
                                        <CheckBoxIcon className="checkicon icon_checked" />
                                    </label>
                                </li>
                            ) */
                        }
                        {/*  */
                            activeTab === 'topic' &&
                            state.generalStates.TOPICS_MY.map((topic,i) =>
                                <li key={topic.id}>
                                    <label>
                                        {/* <img src="images/noimg.jpg" alt="profile noimg" /> */}
                                        <ChatIcon className="icon_room" />
                                        <p className="name">{topic.name}</p>
                                        <input type="radio" ref={(elem) => (inputRef.current[i]=elem)} name="input_relay" value={topic.id} />
                                        <CheckBoxOutlineBlankIcon className="checkicon icon_unchecked" />
                                        <CheckBoxIcon className="checkicon icon_checked" />
                                    </label>
                                </li>
                            )
                        }
                    </ul>
                </div>
            </div>
            <div className="btm">
                <button onClick={clickSubmit} className="btn_submit"></button>
                <button onClick={clickCancel} className="btn_cancel"></button>
            </div>
        </div>
    );
}

export default ItemRelay;
