import React, { useState, useContext, useEffect, useRef } from 'react';
import MyList from './MyList';
import AllList from './AllList';
import UserList from './UserList';
import ListNavi from './ListNavi';
import CreateTopic from './CreateTopic';
import AddIcon from '@material-ui/icons/Add';
import {StoreContext} from "../../../context/store/storeContext";
import {MetaRequestTopicList, MetaRequestAllUser, MetaRequestTopicListMe} from "../../../utils/nanoit/meta"
import BtnRefresh from '../BtnRefresh';


function List() {
    const {state, actions} = useContext(StoreContext);

    const [viewType, setViewType] = useState('UserList'); // AllList, MyList, UserList
    
    //   =>    
    useEffect(() => {
        if(viewType==="AllList") actions.generalActions.WebsocketSendData(MetaRequestTopicList());
    }, [viewType==='AllList']);

    //   =>    
    useEffect(() => {
        if(viewType==="UserList") {
            actions.generalActions.WebsocketSendData(MetaRequestAllUser());
        }
    }, [viewType==='UserList']);

    //  UI SHOW     
    const chatRef = useRef();
    useEffect(() => {
        chatRef.current.scrollTop = 0;
    }, [state.viewStates.isShowCreateTopic]);

    /**
     *  ( )
     */
    const refreshAllUser = () => actions.generalActions.WebsocketSendData(MetaRequestAllUser());
    const refreshAllTopic = () => actions.generalActions.WebsocketSendData(MetaRequestTopicList());
    /* const refreshMyTopic = () => actions.generalActions.WebsocketSendData(MetaRequestTopicListMe()); */


    return (
        <div className="list" ref={chatRef} isoverflow={state.viewStates.isShowCreateTopic===true ? 'hidden' : ''}>
            <ListNavi 
                viewType={viewType}
                setViewType={setViewType} 
            />

            { viewType ==='UserList' && 
                <>
                    <div className="list_title">
                          
                        {/* <BtnRefresh refresh={refreshAllUser} />  */}
                    </div>
                    <UserList />
                </>
            }
            
            { viewType ==='AllList'  && 
                <>
                    <div className="list_title">
                         
                        {/* <BtnRefresh refresh={refreshAllTopic} />  */}
                    </div>
                    <AllList setViewType={setViewType} />
                </>
            }
            
            { viewType ==='MyList'   && 
                <>
                    <div className="list_title">
                         
                        {/* <BtnRefresh 
                            // refresh={refreshMyTopic} 
                        />  */}
                        </div>
                    <MyList />
                </>
            }

            <CreateTopic setViewType={setViewType} />
            <button 
                className="btn_create_mobile" 
                onClick={()=> actions.viewActions.isShowCreateTopic(true)}>
                <AddIcon />
            </button>
        </div>
    );
}

export default React.memo(List);
