import React, { useContext, useEffect, useState, useCallback } from 'react';
import { DELETE_MY_TOPIC_BY_TOPIC_ID } from '../../../context/actions/dispatchs';
import { StoreContext } from "../../../context/store/storeContext";
import { CtrlTopicJoin } from "../../../utils/nanoit/ctrl";
import { MetaRequestTopicMessage, MetaRequestUserTopicId } from '../../../utils/nanoit/meta';


function AllList({ setViewType }) {
    const { state, actions } = useContext(StoreContext);
    const [topicRemainder, setTopicRemainder] = useState([]);

    const clickJoin = (topic_id) => {
        if (window.confirm('   ?')) {
            actions.generalActions.WebsocketSendData(CtrlTopicJoin(topic_id));
            actions.viewActions.selecTopic(topic_id);
            actions.viewActions.isShowChatMobile(true);

            actions.generalActions.WebsocketSendData(MetaRequestTopicMessage(topic_id)); //   
            // actions.generalActions.WebsocketSendData(MetaRequestUserTopicId(topic_id)); //    => viewApp 

            // UI  ,    delay ,      
            actions.viewActions.isShowChatboxLoading(false);
            setTimeout(function(){
                actions.viewActions.isShowChatboxLoading(true);
            }, 10);

            setViewType('MyList');
        }
    }


    const Topics = useCallback(({ topics, clickJoin }) =>
        topics.length !==0 &&
        topics?.map((topic, i) =>
        <li
            key={i}
            topicid={topic.id}
            owner={topic.owner}
            onClick={() => clickJoin(topic.id)}
        >
            <button>
                <p>{topic.name}</p>
                <span>  : {topic.id}</span>
            </button>
        </li>
    ), []);


    //  -  =       
    useEffect(() => {
        let topic=[];
        state.generalStates.TOPICS_ALL.forEach(all => {
            for(let x of state.generalStates.TOPICS_MY) {
                if(x.id === all.id) return;
            }
            topic.push(all);
        })
        setTopicRemainder(topic);
    }, [state.generalStates.TOPICS_ALL, state.generalStates.TOPICS_MY]);


    return (
        <div className="roomlist AllList">
            <ul>
                <Topics topics={topicRemainder} clickJoin={clickJoin} />
            </ul>
        </div>
    );
}

export default React.memo(AllList);