import React, {useRef, useContext, useEffect} from 'react';
import {StoreContext} from "../../../context/store/storeContext";

function PopupMessage({ setIsShowPopupMessage, getClickMessage, whoIsClickMsg }) {
    const {state, actions} = useContext(StoreContext);

    /**
     * @name closePopup
     * @summary   
     */
    const closePopup = () => setIsShowPopupMessage(false);

    /**
     * @name clickCopy
     * @summary 
     */
    const copyRef = useRef();
    const clickCopy = () => {
        copyRef.current.select();
        document.execCommand("copy");

        closePopup();
    }

    /**
     * @name clickReply
     * @summary 
     */
    const clickReply = () => {
        actions.viewActions.isShowPopupMessageItems(true, 'reply'); //  UI  
        closePopup();
    }

    /**
     * @name clickRelay
     * @summary 
     */
    const clickRelay = () => {
        actions.viewActions.isShowPopupMessageItems(true, 'relay');
        actions.viewActions.tempSaveMsgForRelay(getClickMessage);

        closePopup();
    }

    /**
     * @name clickDelete
     * @summary 
     */
    const clickDelete = () => {
        // actions.generalActions.
        closePopup();
    }

    return (
        <div className="popupMessage">
            <div className="con">
                {
                    // /  X,  
                    !getClickMessage.file &&
                    <>
                        <button onClick={clickCopy}></button>
                        <input type="text" value={getClickMessage.content} className="input_msg_copy" ref={copyRef} readOnly />
                    </>
                }
                {
                    whoIsClickMsg === 'left' &&
                    <button onClick={clickReply}></button>
                }
                {   // / ..
                    getClickMessage.file &&
                    <button onClick={clickRelay}></button>
                }

                <button onClick={clickDelete}></button>
            </div>
            <div className="bg" onClick={closePopup}></div>
        </div>
    );
}

export default PopupMessage;
