import React, {useCallback, useContext, useEffect} from "react";
import {StoreContext} from "../../../context/store/storeContext";
import CloseIcon from '@material-ui/icons/Close';

/**
 * @name SearchUsers
 * @summary   @    '' 
 * @param {setMentionUsers}  
 * @param {setShowSearchUsers}  show/hide
 */
function SearchUsers({msgRef, setShowSearchUsers }) {
    const {state, actions} = useContext(StoreContext);

    const clickGetUser = (user) => {
        actions.viewActions.tempSaveMentionUsers(user);
        setShowSearchUsers(false);
        msgRef.current.focus();
    }

    const clickClosePopup = () => setShowSearchUsers(false);
    
    const Users = () => {

        return(
            <ul>
                {
                    state.generalStates.TOPICS_MY.filter(e => e.id === state.viewStates.selectopicid)[0].users.map((user, i) => {

                        //        
                        if( !user ) return;

                        //      
                        if( user.id === state.generalStates.SESSION_USER_INFO.id ) return;

                        return(
                            <li key={i}>
                                <button onClick={() => clickGetUser(user)}>
                                    {
                                        /**
                                         * @summary  user  profile img    
                                         */
                                        !user?.profile?.file_id
                                        ? <img src="images/noimg.jpg" alt="profile noimg"></img>
                                        : <img src={state.generalStates.FRIENDS_ALL.find(e => e.profile.file_id === user.profile?.file_id).profile.img} />
                                    }
                                    {user.auth?.name}
                                </button>
                            </li>
                        )
                    })
                }
            </ul>
        )
    }

    return (
        <div className="searchUsers">
            <h3>
                <span> </span>
                <button onClick={clickClosePopup}><CloseIcon /></button>
            </h3>
            <Users />
        </div>
    );
}

export default SearchUsers;
