import React, {useContext, useState, useEffect, useMemo} from 'react';
import {StoreContext} from "../../../context/store/storeContext";
import {MetaRequestTopicMessage, MetaRequestUserTopicId} from "../../../utils/nanoit/meta";


function MyList() {
    const {state, actions} = useContext(StoreContext);

    return (
        <div className="roomlist MyList">
            <ul>
                {
                    state.generalStates.TOPICS_MY?.map((topic) => 
                        <li 
                            key={topic.id} 
                            isactive={topic.id == state.viewStates.selectopicid ? 'active' : ''} 
                            onClick={() => {
                                actions.viewActions.selecTopic(topic.id);
                                actions.viewActions.isShowChatMobile(true);
                            }}
                        >
                            <button id={topic.id}>
                                <p>{topic.name}</p>
                                <span>  : {topic.id}</span>
                            </button>
                        </li>
                    )
                }
            </ul>
        </div>
    )
}

export default React.memo(MyList);
