import {uuidv4} from "./auth";

const FileSendAfterUpload = (topicId, fileId, mime) => {
    return {
        msg: {
            uuid: uuidv4(),
            request: {
                what: "message",
                how: "send",
                using: "file",
                topic: {
                    id:topicId
                },
                file: {
                    id:fileId
                },
                mime: mime
            }
        }
    }
}

export {FileSendAfterUpload}