import UID from 'uniquebrowserid';

/**
 * @name deviceDivider
 * @summary    
 * @description 
 *  1.   (MOBILE / PC)
 *  2.   
 *  3.    (https://www.npmjs.com/package/uniquebrowserid)
 */
export const deviceDivider = () => {
    const userAgent = window.navigator.userAgent;
    const isMobile = detectDevice(userAgent);
    const browserid = new UID().completeID();
    
    function detectDevice(agent){
        const mobileRegex = [
            /iPhone/i,
            /iPad/i,
            /iPod/i,
            /Android/i,
            /BlackBerry/i,
            /Windows Phone/i
        ]
        return mobileRegex.some(mobile => agent.match(mobile))
    }

    if (isMobile) {
        console.log(` => (1) DEVICE: MOBILE, (2) AGENT: ${userAgent}, (3) browserId: ${browserid} `)
    }else {
        console.log(` => (1) DEVICE: PC, (2) AGENT: ${userAgent}, (3) browserId: ${browserid} `)
    }
}