import * as dispatch from "../../dispatchs";
import {
    REQUEST_HOW_CREATE,
    REQUEST_HOW_INVITE,
    REQUEST_HOW_JOIN,
    REQUEST_HOW_LEAVE,
    REQUEST_WHAT_TOPIC,
    REQUEST_WHO_ALONE, TYPE_LOGGING_RAW,
    TYPE_LOGGING_STATUS,
    STRING_CTRL_CREATE_CONTENT
} from "../../../globals";
import {MetaRequestAllUser, MetaRequestTopicList, MetaRequestTopicMessage, MetaRequestUserTopicId} from "../../../../utils/nanoit/meta";
import { MsgSendCtrlMessage } from "../../../../utils/nanoit/msg";


function CtrlTopicCreate(root, props) {
    if (root.from.me !== true) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]    ,     ")
        return
    }

    if (root.ctrl.request.who === REQUEST_WHO_ALONE && root.ctrl.response.topic !== undefined) {
        root.ctrl.response.topic.is_owner = true;

        dispatch.InsertMyTopic(props, root.ctrl.response.topic)
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]    ")

        //    
        dispatch.SelecTopicById(props, root.ctrl.response.topic.id);
        dispatch.Writer(props, MetaRequestTopicMessage(root.ctrl.response.topic.id))

        // AFTER CTRL MESSAGE SEND
        dispatch.Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(MsgSendCtrlMessage(root.ctrl.response.topic.id, STRING_CTRL_CREATE_CONTENT)))
        dispatch.Writer(props, MsgSendCtrlMessage(root.ctrl.response.topic.id, STRING_CTRL_CREATE_CONTENT))
    } else {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]   ")
    }
}

function CtrlTopicInvite(root, props) {
    console.log(" ~ file: index.js ~ line 42 ~ CtrlTopicInvite ~ root, props", root.ctrl.response.topic.users[root.ctrl.response.topic.users.length-1])
    if (root.from.me === true) {
        if (root.ctrl.response.topic !== undefined && root.ctrl.response.topic.users !== undefined) {

            //      
            dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]      ")

            dispatch.getToastPopup(props, '  .');
            
            // STEP 1 :     , reducer   
            root.ctrl.response.topic.users.forEach((user) => {
                dispatch.InsertUserChangeInfo(props, {id:root.ctrl.response.topic.id} , user)
            })

            // STEP 2 :     
            dispatch.Writer(props, MetaRequestTopicMessage(root.ctrl.request.topic.id))
        } else {
            dispatch.getToastPopup(props, '  .');
            dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]      . response  response.topic ")
        }
    } else {
        if (root.ctrl.response.topic !== undefined && root.ctrl.response.topic.users !== undefined) {
            if (root.ctrl.request.user_id === props.state.generalStates.SESSION_USER_INFO.id) {
                //      
                dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]      ")

                // STEP 1 :  
                root.ctrl.response.topic.is_owner = false
                dispatch.InsertMyTopic(props, root.ctrl.response.topic)

                // STEP 2 :       ,  
                // dispatch.Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(MetaRequestTopicMessage(root.ctrl.request.topic_id)))
                dispatch.Writer(props, MetaRequestTopicMessage(root.ctrl.request.topic.id))
            } else {
                //     
                dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]      ")

                // STEP 1 :     , reducer   
                root.ctrl.response.topic.users.forEach((user) => {
                    dispatch.InsertUserChangeInfo(props, {id:root.ctrl.response.topic.id} , user)
                })
            }
        } else {
            dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]  . response  response.topic ")
        }
    }
}

function CtrlTopicJoin(root, props) {

    if (root.from.me === true) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]   ")

        // STEP 1 :    TOPIC 
        root.ctrl.response.topic.is_owner = false
        dispatch.InsertMyTopic(props, root.ctrl.response.topic)

        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[SEND] " + JSON.stringify(MetaRequestUserTopicId(root.ctrl.response.topic.id)))
        
        //    
        dispatch.Writer(props, MetaRequestTopicMessage(root.ctrl.response.topic.id))

        //    
        dispatch.Writer(props, MetaRequestUserTopicId(root.ctrl.response.topic.id))
    } else {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]    ")


        // STEP 1 :   
        root.ctrl.response.topic.users.forEach((user) => {
            dispatch.InsertUserChangeInfo(props, {id:root.ctrl.response.topic.id} , user)
        })

        // STEP 2 :         
        dispatch.Writer(props, MetaRequestTopicMessage(root.ctrl.response.topic.id))

        //          
        dispatch.Writer(props, MetaRequestAllUser());
    }
}

function CtrlTopicLeave(root, props) {
    if (root.from.me === true) {
        dispatch.DeleteMyTopicByTopicId(props, {id: root.ctrl.request.topic.id})
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]   ")

        //  
        // setLocalExitTopic(props.state.generalStates.SESSION_USER_INFO.id, props.state.viewStates.selectopicid);

        //        
        dispatch.Writer(props, MetaRequestTopicMessage(root.ctrl.request.topic.id))

        //      ,    
    } else {
        dispatch.DeleteUserByTopicId(props, {id: root.ctrl.request.topic.id}, root.from.user)
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]   ")
        
        //        
        dispatch.Writer(props, MetaRequestTopicMessage(root.ctrl.request.topic.id))
    }
    
    dispatch.Writer(props, MetaRequestTopicList())
}

export const ParserCtrl = (props, root) => {
    if (root.ctrl.request === undefined) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] request  ");
        return
    }

    if (root.ctrl.request.what === REQUEST_WHAT_TOPIC && root.ctrl.request.how === REQUEST_HOW_CREATE) {
        CtrlTopicCreate(root, props);
    } else if (root.ctrl.request.what === REQUEST_WHAT_TOPIC && root.ctrl.request.how === REQUEST_HOW_INVITE) {
        CtrlTopicInvite(root, props);
    } else if (root.ctrl.request.what === REQUEST_WHAT_TOPIC && root.ctrl.request.how === REQUEST_HOW_JOIN) {
        CtrlTopicJoin(root, props);
    } else if (root.ctrl.request.what === REQUEST_WHAT_TOPIC && root.ctrl.request.how === REQUEST_HOW_LEAVE) {
        CtrlTopicLeave(root, props);
    } else {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] request   ");
    }
}