import {uuidv4} from "./auth";

const CtrlTopicCreate = (topicName, who, usePrivate) => {
    if (topicName === undefined || topicName === "") {
        return {
            ctrl: {
                uuid: uuidv4(),
                request: {
                    what: "topic",
                    how: "create",
                    who: who,
                    private: usePrivate,
                    topic: {
                        name: topicName
                    }
                }
            }
        }
    } else {
        return {
            ctrl: {
                uuid: uuidv4(),
                request: {
                    what: "topic",
                    how: "create",
                    who: who,
                    private: usePrivate,
                    topic: {
                        name: topicName
                    },
                }
            }
        }
    }
}

const CtrlTopicJoin = (id) => {
    return {
        ctrl: {
            uuid: uuidv4(),
            request: {
                what: "topic",
                how: "join",
                topic: {
                    id: id
                }
            }
        }
    }
}

const CtrlTopicLeave = (id) => {
    return {
        ctrl: {
            uuid: uuidv4(),
            request: {
                what: "topic",
                how: "leave",
                topic: {
                    id: id
                }
            }
        }
    }
}

const CtrlTopicInvite = (userId, topicId) => {
    return {
        ctrl: {
            uuid: uuidv4(),
            request: {
                what: "topic",
                how: "invite",
                user: {
                    id: userId
                },
                topic: {
                    id: topicId
                }
            }
        }
    }
}

export {CtrlTopicCreate, CtrlTopicJoin, CtrlTopicLeave, CtrlTopicInvite}