import {
    TYPE_ADD_SEND_MESSAGE_IN_TRACE,
    TYPE_DELETE_MY_TOPIC_BY_TOPIC_ID,
    TYPE_DELETE_USER_BY_TOPIC_ID,
    TYPE_DROP_ALL_FRIENDS,
    TYPE_DROP_MY_TOPICS,
    TYPE_INSERT_ALL_FRIENDS,
    TYPE_INSERT_ALL_TOPIC,
    TYPE_INSERT_MESSAGE_BY_TOPIC_ID,
    TYPE_INSERT_MESSAGES_BY_TOPIC_ID,
    TYPE_INSERT_MY_TOPIC,
    TYPE_INSERT_USER_BY_TOPIC_ID,
    TYPE_LOGGING_RAW,
    TYPE_LOGGING_STATUS,
    TYPE_RESET,
    TYPE_UPDATE_IS_WRITE_LOG,
    TYPE_UPDATE_MESSAGE_BY_TOPIC_ID,
    TYPE_UPDATE_SEND_MESSAGE_IN_TRACE,
    TYPE_UPDATE_SESSION_JWT,
    TYPE_UPDATE_SESSION_LOGIN,
    TYPE_UPDATE_SESSION_USER,
    TYPE_UPDATE_WEBSOCKET_STATUS,
    TYPE_UPDATE_PROFILE,
    TYPE_ADD_PROFILE_IMG,
    TYPE_INSERT_USER_CHANGE_INFO,
    TYPE_INSERT_NEW_USER_BY_TOPIC_ID,
    TYPE_UPDATE_MESSAGE_ADD_FILE,
    TYPE_ADD_REPLY,
    TYPE_ADD_MENTION
} from "../globals";

const ws = document.location.host.split(':')[0]==='localhost' ? "ws://" : "wss://";

// link.nanoit.kr
export const generalStates = {
    DEFAULT_USE_LOGGER: true,
    DEFAULT_CHAT_SERVER_URL: "https://" + document.location.host,
    DEFAULT_WEBSOCKET_SERVER_URL: ws + document.location.host + "/ws",
    DEFAULT_NEXT_RAW_LOG_ID: 1,
    DEFAULT_NEXT_STATUS_LOG_ID: 1,
    DEFAULT_RAW_LOG: [],
    DEFAULT_STATUS_LOG: [],

    // WS
    WEBSOCKET_STATUS: "NOT CONNECTED",

    // INFO
    SESSION_JWT_TOKEN: null,
    SESSION_IS_LOGIN: false,
    SESSION_LOGIN_COUNT: 0,
    SESSION_USER_INFO: {
        id: null,
        name: null
    },

    SEND_MESSAGE_TRACE: [],
    FRIENDS_ALL: [],
    TOPICS_ALL: [],
    TOPICS_MY: [],
    REPLY:[],
    MENTION:[]
}

export const generalReducer = (state, action) => {
    switch (action.type) {
        /**
         *  MSG, CTRL, AUTH, META, FILE    
         */
        case TYPE_UPDATE_SEND_MESSAGE_IN_TRACE:
            return {
                ...state,
                SEND_MESSAGE_TRACE: [
                    ...(state.SEND_MESSAGE_TRACE.map(trace => {
                        if (trace.uuid === action.message.uuid) {
                            trace.result = action.message.result
                            trace.status = action.message.status
                            return trace
                        } else {
                            return trace
                        }
                    }))
                ]
            }
        /**
         * MSG, CTRL, AUTH, META, FILE    LOW 
         */
        case TYPE_ADD_SEND_MESSAGE_IN_TRACE:
            return {
                ...state,
                SEND_MESSAGE_TRACE: [
                    ...state.SEND_MESSAGE_TRACE,
                    action.message
                ]
            }
        /**
         *   
         */
        case TYPE_RESET:
            return {
                ...state,
                SESSION_JWT_TOKEN: null,
                SESSION_IS_LOGIN: false,
                SESSION_USER_INFO: {
                    id: null,
                    name: null
                },
                FRIENDS_ALL: [],
                TOPICS_ALL: [],
                TOPICS_MY: [],
            }
        /**
         *   
         */
        case TYPE_LOGGING_STATUS:
            return {
                ...state,
                DEFAULT_NEXT_STATUS_LOG_ID: state.DEFAULT_NEXT_STATUS_LOG_ID + 1,
                DEFAULT_STATUS_LOG: [
                    ...state.DEFAULT_STATUS_LOG,
                    {
                        id: state.DEFAULT_NEXT_STATUS_LOG_ID,
                        text: action.log
                    }
                ]
            }
        /**
         * RAW  
         */
        case TYPE_LOGGING_RAW:
            return {
                ...state,
                DEFAULT_NEXT_RAW_LOG_ID: state.DEFAULT_NEXT_RAW_LOG_ID + 1,
                DEFAULT_RAW_LOG: [
                    ...state.DEFAULT_RAW_LOG, {id: state.DEFAULT_NEXT_RAW_LOG_ID, text: action.log}
                ]
            }
        /**
         * CLIENT WEBSOCKET SESSION    UPDATE
         */
        case TYPE_UPDATE_SESSION_LOGIN:
            return {
                ...state,
                SESSION_IS_LOGIN: action.isLogin,
                SESSION_LOGIN_COUNT: state.SESSION_LOGIN_COUNT + 1
            }
        /**
         * JWT TOKEN 
         */
        case TYPE_UPDATE_SESSION_JWT:
            return {
                ...state,
                SESSION_JWT_TOKEN: action.token
            }
        /**
         * LOGIN USER  
         */
        case TYPE_UPDATE_SESSION_USER:
            return {
                ...state,
                SESSION_USER_INFO: action.user
            }
        /**
         *    UPDATE
         */
        case TYPE_INSERT_ALL_FRIENDS:
            return {
                ...state,
                FRIENDS_ALL: [
                    ...(state.FRIENDS_ALL.find(friend => friend.id === action.user.id) !== undefined ? state.FRIENDS_ALL : state.FRIENDS_ALL.concat(action.user))
                ]
            }
        /**
         *    
         */
        case TYPE_UPDATE_IS_WRITE_LOG:
            return {
                ...state,
                DEFAULT_USE_LOGGER: action.isWriteLog
            }
        /**
         *
         */
        case TYPE_UPDATE_WEBSOCKET_STATUS:
            return {
                ...state,
                WEBSOCKET_STATUS: action.status
            }
        /**
         * CREATE TOPIC
         * -  TOPIC   TOPIC 
         */
        case TYPE_INSERT_ALL_TOPIC:
            return {
                ...state,
                TOPICS_ALL: [
                    ...(state.TOPICS_ALL.filter(topic => topic.id !== action.topic.id).concat(action.topic))
                ]
            }
        /**
         * CREATE TOPIC
         * -  TOPIC   TOPIC 
         */
        case TYPE_INSERT_MY_TOPIC:
            return {
                ...state,
                TOPICS_MY: [
                    ...(state.TOPICS_MY.filter(topic => topic.id !== action.topic.id).concat(action.topic))
                ]
            }
        case TYPE_DROP_ALL_FRIENDS:
            return {
                ...state,
                FRIENDS_ALL: []
            }
        case TYPE_DROP_MY_TOPICS:
            return {
                ...state,
                TOPICS_MY: []
            }
        case TYPE_DELETE_MY_TOPIC_BY_TOPIC_ID:
            return {
                ...state,
                TOPICS_MY: [
                    ...(state.TOPICS_MY.filter(topic => topic.id !== action.topic.id))
                ]
            }
        case TYPE_INSERT_USER_BY_TOPIC_ID:
            return {
                ...state,
                TOPICS_MY: [
                    ...(state.TOPICS_MY.map(topic => {
                        if (topic.id === action.topic.id) {
                            return {
                                ...topic,
                                users:action.topic.users
                            }
                        } else {
                            return topic
                        }
                    }))
                ]
            }
        case TYPE_INSERT_NEW_USER_BY_TOPIC_ID:
            return {
                ...state,
                TOPICS_MY: [
                    ...(state.TOPICS_MY.map(topic => {
                        if (topic.id === action.topicId) {
                            return {
                                ...topic,
                                users:[
                                    ...(topic.users.map(user => {
                                        return user!==undefined && user
                                    })),
                                    action.user
                                ]
                            }
                        } else {
                            return topic
                        }
                    }))
                ]
            }
        case TYPE_INSERT_USER_CHANGE_INFO:
            return {
                ...state,
                TOPICS_MY: [
                    ...(state.TOPICS_MY.map(topic => {

                        //     return ()
                        try{
                            if( topic.users?.find(e => e.id === action.user.id) !== undefined ) return topic;
                            if( topic.users === undefined ) return topic;
                        }catch(e){
                            return topic
                        }

                        if (topic.id === action.topic.id) {

                            return {
                                ...topic,
                                users:[
                                    ...topic.users,
                                    state.FRIENDS_ALL.find(e=> e.id === action.user.id)
                                ]
                            }
                        } else {
                            return topic
                        }
                    }))
                ]
            }
        case TYPE_DELETE_USER_BY_TOPIC_ID:
            return {
                ...state,
                TOPICS_MY: [
                    ...(state.TOPICS_MY.map(topic => {
                        if (topic.id === action.topic.id) {
                            return {...topic, users: topic.users.filter(user => user.id !== action.user.id)}
                        } else {
                            return topic
                        }
                    }))
                ]
            }
        /**
         *    INSERT 
         * -   SEND_MESSAGE_TRACE JSON  
         * -  INSERT MESSAGE  UUID   INSERT    
         * - UUID   MESSAGE INSERT
         * -   210727
         */
        case TYPE_INSERT_MESSAGE_BY_TOPIC_ID:
            return {
                ...state,
                TOPICS_MY: [
                    // ...state.TOPICS_MY.map(topic => {
                    //     topic.id !== action.topic_id ? topic : topi
                    // })
                    ...(state.TOPICS_MY.map(topic => {
                                if (topic.id === action.topic_id && topic.messages !== undefined  ) {
                                    if (topic.messages.find(message => message.clint_uuid === action.message.clint_uuid) === undefined ) {
                                        return {
                                            ...topic,
                                            messages: [
                                                ...topic.messages,
                                                action.message
                                            ]
                                        }
                                    } else {
                                        return topic
                                    }
                                } else {
                                    return topic
                                }
                            }
                        )
                    )
                ]
            }
        /**
         *    INSERT
         * - META 
         * -   210727
         */
        case TYPE_INSERT_MESSAGES_BY_TOPIC_ID:
            return {
                ...state,
                TOPICS_MY: [
                    ...(state.TOPICS_MY.map(topic => {
                                if (topic.id === action.topic.id) {
                                    return {
                                        ...topic,
                                        messages: action.topic.messages
                                    }
                                } else {
                                    return topic
                                }
                            }
                        )
                    )
                ]
            }
        case TYPE_UPDATE_MESSAGE_ADD_FILE:
            return {
                ...state,
                TOPICS_MY: [
                    ...(state.TOPICS_MY.filter(t => t.id !== action.topic.id)),
                    {
                        ...action.topic,
                        messages:[
                            ...(action.topic.messages.filter(t => t.sequence_id < action.sequence_id)),
                            {
                                ...(action.topic.messages.filter(t => t.sequence_id === action.sequence_id))[0],
                                file:action.file
                            },
                            ...(action.topic.messages.filter(t => t.sequence_id > action.sequence_id)),
                        ]
                    }
                ]
            }
        case TYPE_UPDATE_PROFILE:
            return {
                ...state,
                FRIENDS_ALL: [
                    ...(state.FRIENDS_ALL.map(user => {
                                if (user.id === action.userid) {
                                    return {
                                        ...user,
                                        profile: {
                                            file_id:action.fileid
                                        }
                                    }
                                } else {
                                    return user
                                }
                            }
                        )
                    )
                ]
            }
        case TYPE_ADD_PROFILE_IMG:
            return {
                ...state,
                FRIENDS_ALL: [
                    ...(state.FRIENDS_ALL.map(user => {
                                if (user.id === action.userid) {
                                    
                                    return {
                                        ...user,
                                        profile:{
                                            file_id:action.fileid,
                                            img:action.img
                                        }
                                    }
                                } else {
                                    return user
                                }
                            }
                        )
                    )
                ]
            }
        /**
         *   RESPONSE     UUID  STATUS UPDATE REDUCER
         * -    
         * -   CLIENT    
         * -    RESPONSE   UUID  SEQUENCE ID STATUS  
         */
        case TYPE_UPDATE_MESSAGE_BY_TOPIC_ID:
            return {
                ...state,
                TOPICS_MY: [
                    ...(state.TOPICS_MY.map(topic => {
                                if (topic.id === action.topic_id) {
                                    topic.messages.map(message => {
                                        if (message.uuid === action.message.uuid) {
                                            return {
                                                ...message,
                                                sequence_id: action.message.sequence_id,
                                                status: action.message.status
                                            }
                                        } else {
                                            return message
                                        }
                                    })
                                    // return {
                                    //     ...topic,
                                    //     messages: [
                                    //         ...(topic.messages.map(message => {
                                    //             if (message.uuid === action.message.uuid) {
                                    //                 message.sequence_id = action.message.sequence_id
                                    //                 message.status = action.message.status
                                    //                 return message
                                    //             } else {
                                    //                 return message
                                    //             }
                                    //         }))
                                    //     ]
                                    // }
                                }
                                return topic
                            }
                        )
                    )
                ]
            }
        // REPLY, MENTION
        case TYPE_ADD_REPLY:
            return {
                ...state,
                REPLY:[
                    ...(state.REPLY.filter(r => r.notify_id !== action.notify_id)),
                    {
                        notify_id:action.notify_id,
                        topic_id:action.topic_id,
                        from_user_id:action.from_user_id,
                        from_user_name:action.from_user_name,
                        sequence_id:action.sequence_id,
                    }
                ]
            }
        case TYPE_ADD_MENTION:
            return {
                ...state,
                MENTION:[
                    ...(state.MENTION.filter(r => r.notify_id !== action.notify_id)),
                    {
                        notify_id:action.notify_id,
                        topic_id:action.topic_id,
                        from_user_id:action.from_user_id,
                        from_user_name:action.from_user_name,
                        sequence_id:action.sequence_id,
                    }
                ]
            }
        default:
            return {
                ...state,
                DEFAULT_NEXT_RAW_LOG_ID: state.DEFAULT_NEXT_RAW_LOG_ID + 1,
                DEFAULT_RAW_LOG: [
                    ...state.DEFAULT_RAW_LOG,
                    {
                        id: state.DEFAULT_NEXT_RAW_LOG_ID,
                        text: "[REDUCER-GENERAL] ERROR PARSE " + JSON.stringify(action)
                    }
                ]
            }
    }
}