import { SignalCellularNullRounded } from "@material-ui/icons";
import {
    TYPE_SELECT_TOPIC_ID,
    HIDE_SIGNUP,
    HIDE_LOGIN,
    GET_PROFILE_IMG,
    GET_REPLY_CONTENT,
    IS_SHOW_POPUP_MSG_ITEMS,
    IS_SHOW_CREATE_TOPIC,
    IS_SHOW_CHAT_MOBILE,
    IS_SHOW_INVITE,
    IS_SHOW_CHAT_MEBMER,
    GET_ALL_TOPICS,
    GET_TOPIC_USERS,
    GET_IMAGE_BLOB,
    IS_SHOW_CHATBOX_LOADING,
    IS_SHOW_POPUP_NOTIFY,
    TEMP_SAVE_MENTION_USERS,
    TEMP_SAVE_MSG_FOR_REALY,
    TEMP_SAVE_MSG_FOR_MENTION,
    GET_TOAST_MSG,
    SET_REPLY_INFO,
    SET_MENTION_INFO,
} from "../globals";


export const viewStates = {
    selectopicid:0,
    isShowCreateTopic:false,
    isShowChatMobile:false,
    isShowInvite:false,
    isShowChatUser:false,
    isChatboxLoading:false,
    isShowNotify:false,
    topicsAll:[
        /*
            {
                owner:number,
                sequence_id:number,
                topic_id:number,
                topic_name:string
            }
        */
    ],
    topicUsers:[
        /*
            {
                user_id:number,
                user_name:string,
            }
        */
    ],
    sendImage:null,
    hideSignup:false,
    hideLogin:false,
    profileImg:null,
    replyContent:{
        from_user_id:0,
        from_user_name:'',
        content:'',
        sequence_id:0
    },
    popupMessageItems:{
        reply:false,
        relay:false,
        delete:false,
    },
    mentionUsers:[],
    msgForRelay:'',
    msgForMention:[],
    /* replies:[],
    mentions:[], */
    getToastMsg:null,
};

export const viewReducer = (state, action) => {
    switch (action.type) {
        case TYPE_SELECT_TOPIC_ID:
            return {
                ...state,
                selectopicid:action.selectopicid,
            }
        case IS_SHOW_CREATE_TOPIC:
            return {
                ...state,
                isShowCreateTopic:action.isShow,
            }
        case IS_SHOW_CHAT_MOBILE:
            return {
                ...state,
                isShowChatMobile:action.isShow,
            }
        case IS_SHOW_INVITE:
            return {
                ...state,
                isShowInvite:action.isShow,
            }
        case IS_SHOW_CHAT_MEBMER:
            return {
                ...state,
                isShowChatUser:action.isShow,
            }
        case IS_SHOW_POPUP_NOTIFY:
            return {
                ...state,
                isShowNotify:action.isShow,
            }
        case GET_TOAST_MSG:
            return {
                ...state,
                getToastMsg:action.msg,
            }
        case GET_ALL_TOPICS:
            return {
                ...state,
                topicsAll:action.topicsAll,
            }
        case GET_TOPIC_USERS:
            return {
                ...state,
                topicUsers:action.topicUsers,
            }
        case GET_IMAGE_BLOB:
            return {
                ...state,
                sendImage:action.sendImage,
            }
        case IS_SHOW_CHATBOX_LOADING:
            return {
                ...state,
                isChatboxLoading:action.isShow,
            }
        case HIDE_SIGNUP:
            return {
                ...state,
                hideSignup:action.isShow,
            }
        case HIDE_LOGIN:
            return {
                ...state,
                hideLogin:action.isShow,
            }
        case GET_PROFILE_IMG:
            return {
                ...state,
                profileImg:action.file,
            }
        case GET_REPLY_CONTENT:
            return {
                ...state,
                replyContent:action.msg,
            }
        case IS_SHOW_POPUP_MSG_ITEMS:
            let data;
            if( action.item === 'reply' ) {
                data = { ...state.popupMessageItems, reply:action.isShow }
            }else if( action.item === 'relay' ){
                data = { ...state.popupMessageItems, relay:action.isShow }
            }else if( action.item === 'delete' ){
                data = { ...state.popupMessageItems, delete:action.isShow }
            }
            return {
                ...state,
                popupMessageItems:data
            }
        case TEMP_SAVE_MENTION_USERS:
            return {
                ...state,
                mentionUsers:(
                    !!action.user
                ) ? (
                    [
                        ...(state.mentionUsers.filter(u => u.id !== action.user.id)),
                        {
                            id:action.user.id,
                            name:action.user.auth.name
                        }
                    ]
                ) : ( 
                    []
                )
            }
        case TEMP_SAVE_MSG_FOR_REALY:
            return {
                ...state,
                msgForRelay:action.msg
            }
        case TEMP_SAVE_MSG_FOR_MENTION:
            return {
                ...state,
                msgForMention:action.msg
            }
        default:
            return state
        
      /*   case SET_REPLY_INFO:
        return {
            ...state,
            replies:action.replies
        }
        case SET_MENTION_INFO:
        return {
            ...state,
            mentions:action.mentions
        } */
    }
}