import React, { useCallback } from 'react';
import EmojiData from '../../../emoji/emoji.json'


function Emoji({ viewEmoji, setViewEmoji,  setHaveEmoji }) {

    const EmojiList = useCallback(() => {

        return(
            EmojiData.emoji.map((emoji, i) =>
                <li key={i} onClick={() => {
                    setHaveEmoji(emoji);
                    setViewEmoji(false);
                }}>
                    {emoji}
                </li>
            )
        )
    }, []);


    return (
        <div className="emoji">
            <div className="popup_emoji" isshow={viewEmoji==true ? 'active' : ''}>
                <ul>
                    <EmojiList />
                </ul>
            </div>
        </div>
    );
}

export default React.memo(Emoji);
