import React, { useContext } from 'react';
import {StoreContext} from "../../../context/store/storeContext";
import SubdirectoryArrowRightIcon from '@material-ui/icons/SubdirectoryArrowRight';

function ItemReply() {
    const {state, actions} = useContext(StoreContext);

    return (
        <div className="ItemReply">
            <div className="top">
                <SubdirectoryArrowRightIcon className="icon_reply" />
                <span>{state.viewStates.replyContent.from_user_name}  </span>
            </div>
            <div className="btm">
                <span>{state.viewStates.replyContent.content}</span>
            </div>
        </div>
    );
}

export default ItemReply;
