import React, { useState, useEffect, useContext, useCallback  } from 'react';
import CheckIcon from '@material-ui/icons/Check';
import {StoreContext} from "../../../context/store/storeContext";
import {CtrlTopicInvite} from "../../../utils/nanoit/ctrl";
import { MetaRequestAllUser } from '../../../utils/nanoit/meta';

function PopupInvite() {
    const {state, actions} = useContext(StoreContext);

    const [user, setUser] = useState([]); //  id

    //   
    const topic = state.generalStates.TOPICS_MY.filter(e => e.id === state.viewStates.selectopicid)[0];


    //    
    useEffect(() => {
        state.viewStates.isShowInvite===false && setUser([]);
    }, [state.viewStates.isShowInvite]);

    //    
    const clickUser = userid => {
        const idx = user.indexOf(userid); //   
        const newUser = user.slice();     // 
        
        if( idx === -1 ){  //  
            setUser( user.concat(userid) );
        }else{             //  
            newUser.splice(idx,1);
            setUser(newUser);
        }
    }

    //   
    const handleOnClickInvite = () => {
        user.map(user_id => {
            actions.generalActions.WebsocketSendData(CtrlTopicInvite(user_id, state.viewStates.selectopicid))
        })

        if(user.length===0){
            actions.viewActions.getToast('  ')
            return
        }else{
            // alert(' ')
            actions.viewActions.isShowInvite(false)
        }
    }

    // 
    const Users = ({ topic, chkUser, clickUser }) => {
        const topicUserArr = [];

        topic?.users?.forEach((user) => {
            if( user === undefined ) return; //        
            topicUserArr.push(user.id);
        })
        
        return (
            state.generalStates.FRIENDS_ALL.map((user, i) =>
                topicUserArr.indexOf(user.id) === -1 && //   
                <button onClick={() => clickUser(user.id)} key={i}>
                    <div className="left">
                        {
                            /**
                             * @summary  user  profile img    
                             */
                            !user.profile.file_id
                            ? <img src="images/noimg.jpg" alt="profile noimg"></img>
                            : <img src={state.generalStates.FRIENDS_ALL.find(e => e.profile.file_id === user.profile.file_id).profile.img} />
                        }
                        <span>{user.auth.name}</span>
                    </div>
                    <div className="right">
                        {
                            chkUser.indexOf(user.id) > -1 && <CheckIcon className="icon_check" />
                        }
                    </div>
                </button>
            )
        )
    }


    return (
        <div className="popup_invite popup_right" isshow={state.viewStates.isShowInvite==true ? 'active' : ''}>
            <div className="invite_header">
                <button onClick={() => actions.viewActions.isShowInvite(false)}></button>
                <button 
                    onClick={handleOnClickInvite} 
                    className="btn_confirm">
                    
                </button>
            </div>
            <div className="numbox">
                <span className="num">{user.length}</span>
                <span> </span>
            </div>
            <Users topic={topic} chkUser={user} clickUser={clickUser} />
        </div>
    );
}

export default React.memo(PopupInvite);
