import {device_info_create} from "../navjs"


const uuidv4 = () => {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        let r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });

/* https://nanoit.notion.site/Multi-Device-ddd21a639b2141ef8ee2a9f14bd1e010
    var navigator_info = window.navigator;
    var screen_info = window.screen;
    var uid = navigator_info.mimeTypes.length;
    uid += navigator_info.userAgent.replace(/\D+/g, '');
    uid += navigator_info.plugins.length;
    uid += screen_info.height || '';
    uid += screen_info.width || '';
    uid += screen_info.pixelDepth || '';
    console.log(uid);
    document.write(uid);
 */
}

const device_info = () => {
    const { browser_id, user_agent, device_id, platform, operation_system } = device_info_create();

    return {
        browser_id:browser_id,
        user_agent:user_agent,
        device_id:device_id,
        platform:platform,
        operation_system:operation_system,
    }
}



const AuthLoginIdPw = (email, password) => {

    return {
        auth: {
            uuid: uuidv4(),
            request: {
                what: "session",
                how: "login",
                using: "id",
                user: {
                    auth: {
                        email: email,
                        password: password
                    },
                    device_info:device_info()
                }
            }
        }
    }
}

const AuthLoginTokenSelect = () => {
    return {
        auth: {
            uuid: uuidv4(),
            request: {
                what: "token",
                how: "select"
            }
        }
    }
}

const AuthLoginToken = (token) => {

    return {
        auth: {
            uuid: uuidv4(),
            request: {
                what: "session",
                how: "login",
                using: "token",
                token: token,
                user:{
                    device_info:device_info()
                }
            }
        }
    }
}

const AuthLoginJwt = (token) => {
    if (token === undefined || token === "") {
        return {
            auth: {
                uuid: uuidv4(),
                request: {
                    what: "session",
                    how: "login",
                    using: "key",
                }
            }
        }
    } else {
        return {
            auth: {
                uuid: uuidv4(),
                request: {
                    what: "session",
                    how: "login",
                    using: "key",
                    token: token
                }
            }
        }
    }
}

const AuthLoginAnonymous = (userName) => {
    
    if (userName === undefined || userName === "") {
        return {
            auth: {
                uuid: uuidv4(),
                request: {
                    what: "session",
                    how: "login",
                    using: "anonymous",
                    user: {
                        device_info:device_info()
                    }
                }
            }
        }
    } else {
        return {
            auth: {
                uuid: uuidv4(),
                request: {
                    what: "session",
                    how: "login",
                    using: "anonymous",
                    user_name: userName
                }
            }
        }
    }
}

