const AuthSignup = (name, email, password) => {

    return {
        auth: {
            uuid: uuidv4(),
            request: {
                what: "user",
                how: "register",
                user: {
                    auth: {
                        name: name,
                        email: email,
                        password: password
                    }
                },
                device_info:device_info()
            }
        }
    }
}

export {AuthLoginIdPw, AuthLoginRotary, AuthLogoutNormal, AuthLoginAnonymous, AuthLoginTokenSelect, AuthLoginJwt, AuthLogoutKeepTrue, AuthLogoutKeepFalse, AuthLogoutRotary, uuidv4, AuthSignup, AuthLoginToken}