import React from 'react';
import SettingsIcon from '@material-ui/icons/Settings';
import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import ModeCommentIcon from '@material-ui/icons/ModeComment';
import SupervisorAccountIcon from '@material-ui/icons/SupervisorAccount';

function ListNavi({ viewType, setViewType }) {
    const changeClassName = (typeName) => typeName === viewType ? 'active' : '';
    
    return (
        <div className="listNavi">
            
            <button
                title=" " 
                className={changeClassName('UserList')}
                onClick={() => setViewType('UserList')}>   
                <SupervisorAccountIcon />
            </button>
            
            <button 
                title=" "
                className={changeClassName('AllList')} 
                onClick={() => setViewType('AllList')}>
                <QuestionAnswerIcon />
            </button>

            <button 
                title=" "
                className={changeClassName('MyList')} 
                onClick={() => setViewType('MyList')}>
                <ModeCommentIcon />
            </button>
            
        </div>
    );
}

export default React.memo(ListNavi);
