let writer

function GetUUID(json) {
    let uuid
    if (json.msg !== undefined) {
        uuid = json.msg.uuid
    } else if (json.auth !== undefined) {
        uuid = json.auth.uuid
    } else if (json.meta !== undefined) {
        uuid = json.meta.uuid
    } else if (json.ctrl !== undefined) {
        uuid = json.ctrl.uuid
    } else if (json.notify !== undefined) {
        uuid = json.notify.uuid
    }
    return uuid;
}

export function ReceiveData(json, props) {
    let root
    try {
        root = JSON.parse(json)
    } catch (e) {
        console.log(json)
        console.log(e)
        return
    }

    Logger(props, TYPE_LOGGING_RAW, "[RECV] " + json)
    if (root.ack !== undefined) {
        if (root.ack.uuid === undefined) {
            Logger(props, TYPE_LOGGING_RAW, "[RECV] " + root)
            return
        }

        if (root.result !== undefined && root.result.status_code === 200) {
            SendMessageTraceUpdate(props, root.ack.uuid, TRACE_STATUS_ACK, TRACE_RESULT_PROC)
        } else {
            SendMessageTraceUpdate(props, root.ack.uuid, TRACE_STATUS_DONE, TRACE_RESULT_FAIL)
        }
        return
    }

    if (root.from === undefined) {
        Logger(props, TYPE_LOGGING_STATUS, "[RECV] from  ")
        return
    }

    // if (root.result === undefined) {
    //     SendMessageTraceUpdate(props, GetUUID(json), TRACE_RESULT_FAIL, TRACE_STATUS_DONE)
    //     Logger(props, TYPE_LOG_STATUS, "[RECV] result  ");
    //     return true
    // } else if (root.result.status_code !== 200) {
    //     Logger(props, TYPE_LOG_STATUS, "[RECV] status_code   ");
    //     if (root.auth !== undefined) {
    //         if (root.auth.request.what === REQUEST_WHAT_SESSION && root.auth.request.how === REQUEST_HOW_LOGIN) {
    //             UpdateSessionLogin(props, false)
    //         }
    //     }
    //
    //     // result done
    //     SendMessageTraceUpdate(props, GetUUID(json), TRACE_RESULT_FAIL, TRACE_STATUS_DONE)
    // }
    if (root.auth !== undefined) {
        if (root.result === undefined || root.result.status_code !== 200) {
            SendMessageTraceUpdate(props, root.auth.uuid, TRACE_RESULT_FAIL, TRACE_STATUS_DONE)
            Logger(props, TYPE_LOGGING_STATUS, "[RECV] root.auth == result object  status_code   ");
            if (root.auth !== undefined) {
                if (root.auth.request.what === REQUEST_WHAT_SESSION && root.auth.request.how === REQUEST_HOW_LOGIN) {
                    UpdateSessionLogin(props, false)
                }
            }

            // send error message to view, slack
            if (root.auth.request.what === REQUEST_WHAT_SESSION && root.auth.request.how === REQUEST_HOW_LOGIN) {
                if( root.result?.cause.indexOf('user.id is already login') !== -1  ){
                    getToastPopup(props, `  ,    `);
                }else if(root.result?.cause.indexOf('id pw login fail') !== -1 ) {
                    getToastPopup(props, `,   .`);
                }else{
                    getToastPopup(props, `  .`);
                    sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 122 ~ root.auth, errorinfo :   ${root.result?.cause}`);
                }
            } else if (root.auth.request.what === REQUEST_WHAT_SESSION && root.auth.request.how === REQUEST_HOW_LOGOUT) {
                getToastPopup(props, `  .`);
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 122 ~ root.auth, errorinfo :   ${root.result?.cause}`);
            } else if (root.auth.request.what === REQUEST_USING_TOKEN && root.auth.request.how === REQUEST_HOW_SELECT) {
                getToastPopup(props, `JWT   .`);
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 122 ~ root.auth, errorinfo : JWT   ${root.result?.cause}`);
            } else if (root.auth.request.what === REQUEST_WHAT_USER && root.auth.request.how === REQUEST_HOW_REGISTER) {
                if( root.result?.cause.indexOf('email is already') !== -1 ){
                    getToastPopup(props, `  .`);
                }else{
                    getToastPopup(props, `  .`);
                    sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 122 ~ root.auth, errorinfo :   ${root.result?.cause}`);
                }
            }else{
                getToastPopup(props, `  .`);
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 122 ~ root.auth, errorinfo : else ${root.result?.cause}`);
            }

        } else {
            SendMessageTraceUpdate(props, root.auth.uuid, TRACE_RESULT_DONE, TRACE_STATUS_DONE)
            ParserAuth(props, root)
        }
    } else if (root.meta !== undefined) {
        if (root.result === undefined || root.result.status_code !== 200) {
            SendMessageTraceUpdate(props, root.meta.uuid, TRACE_RESULT_FAIL, TRACE_STATUS_DONE)
            Logger(props, TYPE_LOGGING_STATUS, "[RECV] root.meta == result object  status_code   ");

            // send error message to view, slack
            if (root.meta.request.what === REQUEST_WHAT_TOPIC && root.meta.request.how === REQUEST_HOW_SELECT) {
                if( root.result?.cause.indexOf('topic count is zero') !== -1 ){
                    // getToastPopup(props, `  .`);
                    console.log('   ');
                }else{
                    if(root.result?.cause.indexOf('(BadValue) $in needs an array') !== -1){
                        console.log('   ')
                    }else{
                        getToastPopup(props, `  .`);
                        sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 135 ~ root.meta, errorinfo :   ${root.result?.cause}`);
                    }
                }
            } else if (root.meta.request.what === REQUEST_WHAT_USER && root.meta.request.how === REQUEST_HOW_SELECT) {
                getToastPopup(props, `  .`);
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 135 ~ root.meta, errorinfo :   ${root.result?.cause}`);
            } else if (root.meta.request.what === REQUEST_WHAT_MESSAGE && root.meta.request.how === REQUEST_HOW_SELECT) {
                if( root.result?.cause.indexOf('mongo: no documents in result') !== -1 ){
                    // getToastPopup(props, `  .`);
                    console.log('mongo: no documents in result')
                }else{
                    getToastPopup(props, `  .`);
                    sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 135 ~ root.meta, errorinfo :   ${root.result?.cause}`);
                }
            } else {
                getToastPopup(props, `  .`);
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 135 ~ root.meta, errorinfo : else ${root.result?.cause}`);
            }

        } else {
            SendMessageTraceUpdate(props, root.meta.uuid, TRACE_RESULT_DONE, TRACE_STATUS_DONE)
            ParserMeta(props, root)
        }
    } else if (root.ctrl !== undefined) {
        if (root.result === undefined || root.result.status_code !== 200) {
            SendMessageTraceUpdate(props, root.ctrl.uuid, TRACE_RESULT_FAIL, TRACE_STATUS_DONE)
            Logger(props, TYPE_LOGGING_STATUS, "[RECV] root.ctrl == result object  status_code   ");

            // send error message to view, slack
            if (root.ctrl.request.what === REQUEST_WHAT_TOPIC && root.ctrl.request.how === REQUEST_HOW_CREATE) {
                getToastPopup(props, `  .`)
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 170 ~ root.ctrl, errorinfo :   ${root.result?.cause}`);
            } else if (root.ctrl.request.what === REQUEST_WHAT_TOPIC && root.ctrl.request.how === REQUEST_HOW_INVITE) {
                getToastPopup(props, `  .`)
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 170 ~ root.ctrl, errorinfo :   ${root.result?.cause}`);
            } else if (root.ctrl.request.what === REQUEST_WHAT_TOPIC && root.ctrl.request.how === REQUEST_HOW_JOIN) {
                getToastPopup(props, `  .`)
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 170 ~ root.ctrl, errorinfo :   ${root.result?.cause}`);
            } else if (root.ctrl.request.what === REQUEST_WHAT_TOPIC && root.ctrl.request.how === REQUEST_HOW_LEAVE) {
                getToastPopup(props, `  .`)
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 170 ~ root.ctrl, errorinfo :   ${root.result?.cause}`);
            } else {
                getToastPopup(props, `   .`)
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 170 ~ root.ctrl, errorinfo : else ${root.result?.cause}`);
            }

        } else {
            SendMessageTraceUpdate(props, root.ctrl.uuid, TRACE_RESULT_DONE, TRACE_STATUS_DONE)
            ParserCtrl(props, root)
        }


    } else if (root.msg !== undefined) {
        if (root.result === undefined || root.result.status_code !== 200) {
            SendMessageTraceUpdate(props, root.msg.uuid, TRACE_RESULT_FAIL, TRACE_STATUS_DONE)
            Logger(props, TYPE_LOGGING_STATUS, "[RECV] root.msg == result object  status_code   ");

            // send error message to view, slack
            if (root.msg.request.what === REQUEST_WHAT_MESSAGE && root.msg.request.how === REQUEST_HOW_SEND ) {
                getToastPopup(props, `  .`)
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 221 ~ root.msg, errorinfo :   ${root.result?.cause}`);
            }else if (root.msg.request.what === REQUEST_WHAT_MESSAGE && root.msg.request.how === REQUEST_HOW_CTRL) {

            } else if (root.msg.request.what === REQUEST_WHAT_MESSAGE && root.msg.request.how === REQUEST_HOW_ACK) {

            }else{
                getToastPopup(props, `  .`)
                sendMessageToSlack(`error =>  ~ file: dispatchs.js ~ line 221 ~ root.msg, errorinfo : else ${root.result?.cause}`);
            }

        } else {
            SendMessageTraceUpdate(props, root.msg.uuid, TRACE_RESULT_DONE, TRACE_STATUS_DONE)
            ParserMessage(props, root)
        }
    } else if (root.notify !== undefined) {
        if (root.result === undefined || root.result.status_code !== 200) {
            SendMessageTraceUpdate(props, root.notify.uuid, TRACE_RESULT_FAIL, TRACE_STATUS_DONE)
            Logger(props, TYPE_LOGGING_STATUS, "[RECV] root.notify == result object  status_code   ");
        } else {
            SendMessageTraceUpdate(props, root.notify.uuid, TRACE_RESULT_DONE, TRACE_STATUS_DONE)
            ParserNotify(props, root)
        }
    } else {

    }
}

export function UpdateWriter(sendMessage) {
    writer = sendMessage
}

export function WriterChatMessage(props, json) {
    Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(json))
    SendMessageTrace(props, json)
    if (writer !== undefined) {
        writer(JSON.stringify(json))
    } else {
        console.log("[SEND-FAIL] WEBSOCKET WRITER IS NOT SET")
    }
}

export function Writer(props, json) {
    Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(json))
    SendMessageTrace(props, json)
    if (writer !== undefined) {
        writer(JSON.stringify(json))
    } else {
        console.log("[SEND-FAIL] WEBSOCKET WRITER IS NOT SET")
    }
}

export function SendMessageTraceUpdate(props, uuid, result, status) {
    props.dispatch({
        type: TYPE_UPDATE_SEND_MESSAGE_IN_TRACE,
        message: {
            uuid: uuid,
            result: result,
            status: status,
        }
    })
}

export function SendMessageTrace(props, json) {
    props.dispatch({
        type: TYPE_ADD_SEND_MESSAGE_IN_TRACE,
        message: {
            uuid: GetUUID(json),
            result: TRACE_RESULT_SEND,
            status: TRACE_STATUS_SEND,
            json: json,
        }
    })
}

export function Logger(props, type, log) {
    if (props.state.generalStates.DEFAULT_USE_LOGGER) {
        props.dispatch({
            type: type,
            log: log
        })
    }
}

