import React, { useCallback, useContext } from 'react';
import ReplyIcon from '@material-ui/icons/Reply';
import CloseIcon from '@material-ui/icons/Close';
import SendIcon from '@material-ui/icons/Send';
import {StoreContext} from "../../../context/store/storeContext";
import EmailIcon from '@material-ui/icons/Email';


function Notify() {
    const {state, actions} = useContext(StoreContext);
    const replies = state.generalStates.REPLY;
    const mentions = state.generalStates.MENTION;


    /*******************************************************
     * Functions
     *******************************************************/
    const goToTopicMessage = useCallback((reply) => {
        // console.log(reply)
        actions.viewActions.selecTopic(reply.topic_id);
    },[]);

    /*******************************************************
     * Component
     *******************************************************/
    const MsgReply = () =>
        !replies.length
        ? <p>  </p>
        : replies.map((reply, i) => 
            <a key={i} href={`#reply_${reply.topic_id}_${reply.sequence_id}`} className="msgbox" onClick={() => goToTopicMessage(reply)}>
                <div className="left">
                    <ReplyIcon />
                    <p></p>
                </div>
                <div className="right">
                    <p>{reply.topic_id}</p>
                    <span>{reply.from_user_name}   .</span>
                </div>
                {/* <CloseIcon className="icon_close" /> */}
            </a>
        )

    const MsgMention = () =>
        !mentions.length
        ? <p>  </p>
        : mentions.map((mention, i) => 
            <button className="msgbox">
                <div className="left">
                    <SendIcon />
                    <p></p>
                </div>
                <div className="right">
                    <p>{mention.topic_id}</p>
                    <span>{mention.from_user_name}   .</span>
                </div>
                {/* <CloseIcon className="icon_close" /> */}
            </button>
        )

    // ,      bell_newmsg  
    const BtnViewNotify = () => 
        <button className="btnViewNotify"
            onClick={() => actions.viewActions.isShowPopupNotify(true)}
        >
            <EmailIcon />
            {
                (replies.length || mentions.length ) 
                ? <span className="bell_newmsg"><span className="dot"></span> </span>
                : ''
            }
        </button>


    return (
        <>
        {
            !state.viewStates.isShowNotify && 
            <BtnViewNotify />
        }

        {
            !!state.viewStates.isShowNotify && 
            <div className="Notify">
                <h2>
                    
                    <CloseIcon className="icon_close" onClick={() => actions.viewActions.isShowPopupNotify(false)} />
                </h2>
                <div className="notify_reply">
                    <h3></h3>
                    <MsgReply />
                </div>
                <div className="notify_mention">
                    <h3></h3>
                    <MsgMention />
                </div>
            </div>
        }
        </>
    )
}

export default React.memo(Notify);