import React, {useCallback, useEffect} from 'react';
import {StoreContext} from "../context/store/storeContext";
import useWebSocket, {ReadyState} from "react-use-websocket";
import {TYPE_LOGGING_STATUS} from "../context/globals";

export const InitializeWebsocket = () => {
    const {state, actions} = React.useContext(StoreContext)

    const {sendMessage, lastMessage, readyState, getWebSocket} = useWebSocket(state.generalStates.DEFAULT_WEBSOCKET_SERVER_URL, {
        shouldReconnect: (closeEvent) => true,
        reconnectAttempts: 10,
        reconnectInterval: 3000,
    });

    const connectionStatus = {
        [ReadyState.CONNECTING]: 'Connecting',
        [ReadyState.OPEN]: 'Open',
        [ReadyState.CLOSING]: 'Closing',
        [ReadyState.CLOSED]: 'Closed',
        [ReadyState.UNINSTANTIATED]: 'Uninstantiated',
    }[readyState];

    useEffect(() => {
        if (lastMessage !== null) {
            actions.generalActions.WebsocketReceiveData(lastMessage.data)
        }
    }, [lastMessage])

    useEffect(() => {
        if (connectionStatus === 'Open') {
            console.log('opened');
        } else if (connectionStatus === 'Connecting') {
            console.log('connecting...');
        } else if (connectionStatus === 'Closing') {
            console.log('closing...');
        } else if (connectionStatus === 'Closed') {
            console.log('closed');
            actions.viewActions.getToast(' ...  (websocket closed)');
        } else {
            console.log('uninstantiated');
        }
    }, [connectionStatus])

    useEffect(() => {
        if (connectionStatus === 'Open') {
            actions.generalActions.WebsocketUpdateWriter(sendMessage)
        } else {
            actions.generalActions.ResetAllStates()
            // actions.generalActions.TOPIC_DELETE_ALL(false)
            // actions.generalActions.DELETE(false)
            // actions.generalActions.SESSION_UPDATE_LOGIN(false)
            // actions.generalActions.WebsocketUpdateWriter(undefined)
        }

        actions.generalActions.WebsocketUpdateStatus(connectionStatus)
        actions.generalActions.Logger(TYPE_LOGGING_STATUS, "[WEBSOCKET] STATUS CHANGED = " + connectionStatus)
    }, [connectionStatus])

    const handleWebsocketClose = useCallback(() => {
        getWebSocket().close()
    }, []);

    return {sendMessage, lastMessage, readyState, getWebSocket, handleWebsocketClose}
}