import * as dispatch from "../../dispatchs";
import {Writer} from "../../dispatchs";
import {
    REQUEST_HOW_ACK,
    REQUEST_HOW_CTRL,
    REQUEST_HOW_SEND,
    REQUEST_WHAT_MESSAGE,
    TYPE_LOGGING_STATUS,
    TYPE_LOGGING_RAW,
} from "../../../globals";
import { MsgReceiveAck } from "../../../../utils/nanoit/msg";
import { notifyRequestMentionCreate, notifyRequestReplyCreate } from "../../../../utils/nanoit/notify";

export const ParserMessage = (props, root) => {
    if (root.msg.request === undefined) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] request  ");
        return
    }

    if ( root.msg.request.what === REQUEST_WHAT_MESSAGE &&
        root.msg.request.how === REQUEST_HOW_SEND
    ) {

        let msg = {
            clint_uuid:root.msg.uuid,
            content: root.msg.request.message,
            created_at: new Date(),
            message_type: "send",
            sequence_id: root.msg.response.message.sequence_id,
            topic_id: root.msg.request.topic.id,
        }

        if (root.from.me === true) {
            msg['from_user_id'] = props.state.generalStates.SESSION_USER_INFO.id;
            msg['from_user_name'] = props.state.generalStates.SESSION_USER_INFO.name;
        }else{
            msg['from_user_id'] = root.from.user.id;
            msg['from_user_name'] = root.from.user.auth.name;
        }

        // 
        if( root.msg.request.custom?.notify.reply && !root.msg.request.custom?.notify.mention ){
            msg['custom'] = {
                notify:{
                    reply:{
                        sequence_id:root.msg.request.custom.notify.reply.sequence_id,
                        to_user_id:root.msg.request.custom.notify.reply.to_user_id
                    }
                }
            }
        }

        // 
        if( root.msg.request.custom?.notify.mention && !root.msg.request.custom?.notify.reply ){
            msg['custom'] = {
                notify:{
                    mention:{
                        to_users_id:root.msg.request.custom.notify.mention.to_users_id
                    }
                }
            }
        }

        //  & 
        if( root.msg.request.custom?.notify.reply && root.msg.request.custom?.notify.mention ){
            msg['custom'] = {
                notify:{
                    reply:{
                        sequence_id:root.msg.request.custom.notify.reply.sequence_id,
                        to_user_id:root.msg.request.custom.notify.reply.to_user_id
                    },
                    mention:{
                        to_users_id:root.msg.request.custom.notify.mention.to_users_id
                    }
                }
            }
        }

        //  store 
        dispatch.InsertMessageByTopicId(props, root.msg.request.topic.id, msg)
        // setLocalInsertMessage(props.state.generalStates.SESSION_USER_INFO.id, root.msg.request.topic.id, msg)


        // reply 
        if( root.msg.request.custom?.notify.reply && root.from.me === true ){ 
            Writer(props, notifyRequestReplyCreate(
                root.msg.request.topic.id,
                root.msg.request.custom.notify.reply.to_user_id,
                root.msg.response.message.sequence_id
            ))
        }

        // mention 
        if( root.msg.request.custom?.notify.mention && root.from.me === true ){ 
            Writer(props, notifyRequestMentionCreate(
                root.msg.request.topic.id,
                root.msg.request.custom.notify.mention.to_users_id,
                root.msg.response.message.sequence_id
            ))
        }

        dispatch.Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(MsgReceiveAck(root.msg.request.topic.id, root.msg.response.message.sequence_id)));
        Writer(props, MsgReceiveAck(root.msg.request.topic.id, root.msg.response.message.sequence_id))
    } else if (root.msg.request.what === REQUEST_WHAT_MESSAGE && root.msg.request.how === REQUEST_HOW_CTRL) {

/*
        dispatch.InsertMessageByTopicId(props, root.msg.request.topic.id, {
            uuid: root.msg.uuid,
            status: TRACE_STATUS_DONE,
            message_type: REQUEST_HOW_CTRL,
            sequence_id: root.msg.response.message.sequence_id,
            content: root.msg.request.message,
        })
*/

        // dispatch.InsertMessageByTopicId(props, root.msg.request.topic.id, msg)
        // dispatch.InsertMessageByTopicId(props, root.msg.request.topic.id, root.msg.response.message.sequence_id, root.msg.request.message, root.from.user)

    } else if (root.msg.request.what === REQUEST_WHAT_MESSAGE && root.msg.request.how === REQUEST_HOW_ACK) {

    }
}