import {uuidv4} from "./auth";

const MetaRequestTopicList = () => {
    return {
        meta: {
            uuid: uuidv4(),
            request: {
                what: "topic",
                how: "select"
            }
        }
    }
}

const MetaRequestTopicListMe = () => {
    return {
        meta: {
            uuid: uuidv4(),
            request: {
                what: "topic",
                how: "select",
                using: "me"
            }
        }
    }
}

const MetaRequestAllUser = () => {
    return {
        meta: {
            uuid: uuidv4(),
            request: {
                what: "user",
                how: "select",
            }
        }
    }
}

const MetaRequestUserTopicId = (id) => {
    return {
        meta: {
            uuid: uuidv4(),
            request: {
                what: "user",
                how: "select",
                using: "topic",
                topic: {
                    id:id
                }
            }
        }
    }
}

const MetaRequestTopicMessage = (id) => {
    return {
        meta: {
            uuid: uuidv4(),
            request: {
                what: "message",
                how: "select",
                topic: {
                    id:id
                }
            }
        }
    }
}

const MetaRequestPagingMessage = () => {
    return{
        meta: {
            uuid: uuidv4(),
            request: {
                what: "topic",
                how: "select",
                paging: {
                    created_at: new Date().toISOString() //RFC 3339 format
                }
            }
        }
    }
}


export {MetaRequestTopicList, MetaRequestTopicMessage, MetaRequestTopicListMe, MetaRequestAllUser, MetaRequestUserTopicId, MetaRequestPagingMessage}