import React, {useContext, useState} from 'react';
import {StoreContext} from "../../../context/store/storeContext";

function Header({ isShowNavi, setIsShowNavi }) {
    const {state, actions} = useContext(StoreContext);

    let img = state.generalStates.FRIENDS_ALL.find(e => e.id === state.generalStates.SESSION_USER_INFO.id);
    img = !!img && img.profile.img

    return (
        <header>
            <div className="userinfo">
                <span className="profile">
                    {
                        !img
                        ? <img src="images/noimg.jpg" alt="profile noimg" />
                        : <img src={img} />
                    }
                </span>
                <span className="name">{state.generalStates.SESSION_USER_INFO.name}</span>
            </div>

            <div 
                className="btn_menu" isshow={isShowNavi==true ? 'active' : ''}
                onClick={() => setIsShowNavi(isShowNavi==true ? false : true)}>
                <span></span>
                <span></span>
                <span></span>
            </div>
        </header>
    );
}

export default React.memo(Header);
