import {uuidv4} from "./auth";

const MsgSend = (id, message, msgType, mentionUsersId, replyUserId, sequence_id ) => {
    //  
    let data = {
        msg: {
            uuid: uuidv4(),
            request: {
                what: "message",
                how: "send",
                topic: {
                    id:id
                },
                message: message,
            }
        }
    }

    if( msgType === 'reply' ){ //  
        data.msg.request.custom = {
            notify:{
                reply:{
                    sequence_id:sequence_id,
                    to_user_id:replyUserId,
                },
            },
        }
    }

    if( msgType === 'mention' ){ //  
        data.msg.request.custom = {
            notify:{
                mention:{
                    to_users_id:
                        mentionUsersId.map(user => {
                            return(
                                {id:user.id}
                            )
                        })
                }
            }
        }
    }

    if( msgType === 'reply-mention' ){ //   
        data.msg.request.custom = {
            notify:{
                reply:{
                    sequence_id:sequence_id,
                    to_user_id:replyUserId,
                },
                mention:{
                    to_users_id:
                        mentionUsersId.map(user => {
                            return(
                                {id:user.id}
                            )
                        })
                }
            }
        }
    }
    return data;
}



const MsgReceiveAck = (id, sequence_id) => {
    return {
        msg: {
            uuid: uuidv4(),
            request: {
                what: "message",
                how: "ack",
                topic: {
                    id:id
                },
                sequence_id: sequence_id
            }
        }
    }
}

const MsgSendCtrlMessage = (id, message) => {
    return {
        msg: {
            uuid: uuidv4(),
            request: {
                what: "message",
                how: "ctrl",
                topic: {
                    id:id
                },
                message: message
            }
        }
    }
}

export {MsgSend, MsgReceiveAck, MsgSendCtrlMessage}