import React, {useContext} from 'react';
import ChatHeader from './ChatHeader';
import ChatReciever from './ChatReciever';
import ChatSender from './ChatSender';
import PopupInvite from './PopupInvite';
import PopupUsers from './PopupUsers';
import EmptyCover from './EmptyCover';
import {StoreContext} from "../../../context/store/storeContext";


function Chat() {
    const {state, actions} = useContext(StoreContext);


    return (
        <div className="chat" isshow={state.viewStates.isShowChatMobile===true ? 'active' : ''}>
            <div className="chatWrap">
                <ChatHeader />
                <ChatReciever />
                <ChatSender />
            </div>
            <PopupInvite />
            <PopupUsers />
            <EmptyCover isshow={state.viewStates.isShowChatMobile===true ? '' : 'active'} />
        </div>
    )
}

export default React.memo(Chat);