import * as dispatch from "../../dispatchs";
import {
    REQUEST_HOW_LOGIN, REQUEST_HOW_LOGOUT,
    REQUEST_HOW_SELECT,
    REQUEST_WHAT_SESSION, 
    TYPE_LOGGING_RAW,
    TYPE_LOGGING_STATUS,
    REQUEST_WHAT_USER,
    REQUEST_HOW_REGISTER,
    REQUEST_USING_TOKEN
} from "../../../globals";
import {AuthLoginIdPw, AuthLoginTokenSelect} from "../../../../utils/nanoit/auth";
import {MetaRequestAllUser, MetaRequestTopicList, MetaRequestTopicListMe} from "../../../../utils/nanoit/meta";

function Login(props, root) {
    // dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] login  ");
    // STEP 1 : SESSION  
    dispatch.UpdateSessionLogin(props, true)
    dispatch.UpdateSessionUser(props, root.auth.response.user.id, root.auth.response.user.auth.name)

    // STEP 2 : ID LOGIN   JWT  
    // const loginkey = localStorage.getItem('chattingLoginKey');
    dispatch.Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(AuthLoginTokenSelect()))
    dispatch.Writer(props, AuthLoginTokenSelect())

    // STEP 3 : TOPIC LIST 
    dispatch.Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(MetaRequestTopicListMe()))
    dispatch.Writer(props, MetaRequestTopicListMe())

    // STEP 4 : FRIENDS LIST 
    dispatch.Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(MetaRequestAllUser()))
    dispatch.Writer(props, MetaRequestAllUser())


    // ETC Login component   
    dispatch.viewIsHideLogin(props, true);
    dispatch.viewIsHideSignup(props, true)

    dispatch.getToastPopup(props, ' .');
}

function Logout(props) {
    // LOGOUT   RESET
    dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] logout  ");
    dispatch.getToastPopup(props, ' .');
    dispatch.UpdateSessionLogin(props, false)
    dispatch.UpdateSessionUser(props, null, null)
    dispatch.DropMyTopics(props)
    dispatch.DropAllFriends(props)
}

function Jwt(props, root) {

    // JWT 
    dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] jwt token receive  ")
    dispatch.UpdateSessionJwtToken(props, root.auth.response.jwt);
    localStorage.setItem('chattingLoginKey', root.auth.response.jwt);
}

function Register(props, root) {
    dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] Register  ")

    dispatch.getToastPopup(props, ' .');
    //  UI 
    // dispatch.viewIsHideSignup(props, true)

    //    
    dispatch.Writer(props, AuthLoginIdPw(
            root.auth.request.user.auth.email,
            root.auth.request.user.auth.password,
        )
    )
    // alert(' ')
}


/**
 *  ,   
 * @param props
 * @param root
 * @constructor
 */
export const ParserAuth = (props, root) => {
    //  
    if (root.auth.request === undefined) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] request  ");
        return
    }

    if (root.auth.request.what === REQUEST_WHAT_SESSION && root.auth.request.how === REQUEST_HOW_LOGIN) {
        Login(props, root);
    } else if (root.auth.request.what === REQUEST_WHAT_SESSION && root.auth.request.how === REQUEST_HOW_LOGOUT) {
        Logout(props);
    } else if (root.auth.request.what === REQUEST_USING_TOKEN && root.auth.request.how === REQUEST_HOW_SELECT) {
        Jwt(props, root);
    } else if (root.auth.request.what === REQUEST_WHAT_USER && root.auth.request.how === REQUEST_HOW_REGISTER) {
        Register(props, root);
    }else{
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] AUTH   REQUEST  ");
    }
}