import {uuidv4} from "./auth";

const notifyRequestMentionCreate = (topic_id, users, sequence_id) => {

    return {
        notify: {
            uuid: uuidv4(),
            request: {
                what: "mention",
                how: "create",
                topic: {
                    id: topic_id
                },
                users:users.map(user => {
                        return {id:user.id}
                    })
                ,
                sequence_id: sequence_id
            }
        }
    }
}

/**
 * @param { } topic_id
 * @param {  } user_id
 * @param {   ( X)} sequence_id
 */
const notifyRequestReplyCreate = (topic_id, user_id, sequence_id) => {

    return {
        notify: {
            uuid: uuidv4(),
            request: {
                what: "reply",
                how: "create",
                topic: {
                    id: topic_id
                },
                user: {
                    id: user_id
                },
                sequence_id: sequence_id
            }
        }
    }
}

const replyReceiveAck = (id) => {
    return {
        msg: {
            uuid: uuidv4(),
            request: {
                what: "reply",
                how: "ack",
                notify: {
                    id:id
                },
            }
        }
    }
}

export {notifyRequestMentionCreate, notifyRequestReplyCreate, replyReceiveAck}