import * as dispatch from "../../dispatchs";
import {
    REQUEST_HOW_SELECT, REQUEST_USING_ME, REQUEST_WHAT_MESSAGE,
    REQUEST_WHAT_TOPIC,
    REQUEST_WHAT_USER,
    TYPE_LOGGING_STATUS
} from "../../../globals";
import { getLocalItem, setLocalAddFriendsAll, setLocalAddNewUser, setLocalTopicMessages, setLocalUpdateUser, updateLocalUserTopics } from "../../../localStorageActions";


function MetaSelectTopic(root, props) {
    if (root.meta.response === undefined) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] response  ");
        return
    }

    if (root.meta.response.topics === undefined) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] response.topics  ");
        return
    }

    updateLocalUserTopics(props, root.meta.response.topics);


    // TOPICS ARRAY   
    root.meta.response.topics.forEach(topic => {

        // STEP 1 :  
        topic.owner === props.state.generalStates.SESSION_USER_INFO.id ? topic.is_owner = true : topic.is_owner = false
        if (root.meta.request.using !== undefined && root.meta.request.using === REQUEST_USING_ME) {
            //   
            // setLocalInsertTopic(props.state.generalStates.SESSION_USER_INFO.id, topic);
            

            dispatch.InsertMyTopic(props, topic)

            // STEP 2 :   USER 
            // dispatch.Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(MetaRequestUserTopicId(topic.id)))
            // dispatch.Writer(props, MetaRequestUserTopicId(topic.id))
            
            // STEP 2 :   USER 
/* 
            root.ctrl.response.topic.users.forEach((user) => {
                dispatch.InsertUserChangeInfo(props, {id:root.ctrl.response.topic.id} , user)
            })
*/
            // STEP 3 :   MESSAGE 
            //dispatch.Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(MetaRequestTopicMessage(topic.id)))
            // dispatch.Writer(props, MetaRequestTopicMessage(topic.id))
        } else {
            //   
            dispatch.InsertAllTopic(props, topic)

            // STEP 2 :   USER 
            // dispatch.Logger(props, TYPE_LOGGING_RAW, "[SEND] " + JSON.stringify(MetaRequestUserTopicId(topic.id)))
            // dispatch.Writer(props, MetaRequestUserTopicId(topic.id))
        }
    })
}

function MetaSelectUser(root, props) {
    if (root.meta.request.using !== undefined && root.meta.request.using === "topic") {
        if (root.meta.response.topic !== undefined && root.meta.response.topic.users !== undefined) {
            dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]     ")
            /* root.meta.response.topic.users.forEach(user => {
                dispatch.InsertUserByTopicId(props, {id: root.meta.response.topic.id}, user)
            }) */
            dispatch.InsertUserByTopicId(props, root.meta.response.topic)

            // dispatch.Writer(props, MetaRequestUserTopicId(root.ctrl.response.topic.id))
        } else {
            dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]     ");
        }
    } else {
        if (root.meta.response.users !== undefined) {
            dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]    ")

            /*************************************************************
             * localStorage  =>    x
             * 
             *  localStorage.FRIENDS_ALL    
             * 1.        =>  O
             * 2.   =>  O
             * 3.   ( profile.file_id  ) =>  O,    & profile.file_id .     .
             * 4.   () =>  X
             * 
             * 5.         .
             *************************************************************/
            const GET_LOCAL_FRIENDS_ALL = getLocalItem('NANOIT_CHAT_FRIENDS_ALL');

            // 1-1       
            if( !GET_LOCAL_FRIENDS_ALL ){ 
                setLocalAddFriendsAll(root.meta.response.users);
                root.meta.response.users.forEach(user => dispatch.InsertAllFriends(props, user) )
                console.log('parser/meta =>     ')
            }else{

                const user_id_all = [];

                root.meta.response.users.forEach(user => {
                    user_id_all.push(user.id);

                    const isExistUserByLocal = GET_LOCAL_FRIENDS_ALL.find(f => f.id === user.id)

                    if( !isExistUserByLocal ){
                        // 2.   &    
                        setLocalAddNewUser(user);
                        dispatch.InsertAllFriends(props, user)
                        console.log('parser/meta => localStorage  ')

                        // 4.   ()
                        /*
                         * -  , user.id     .
                         * 1. user.id  id  ,    UPDATE
                         * 2. user.id  , id    
                         */
                    }else{ //   
                        setLocalUpdateUser(user);

                        dispatch.InsertAllFriends(props, user)
                        console.log('parser/meta => localStorage  ')

                        let friend = GET_LOCAL_FRIENDS_ALL.filter(f => f.id === user.id);
                        if( friend.proflie?.file_id && user.profile?.file_id ){
                            friend.profile = {
                                ...friend.profile,
                                file_id:user.profile.file_id
                            }
                            dispatch.updateProfileId(props, user.id, user.profile.file_id)
                            console.log('parser/meta => localStorage   file_id  ')
                        }
                    }
                })
            }
            //   ,    
            // setLocalUpdateFriend(user_id_all);
        } else {
            dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]    ");
        }
    }
}

function MetaSelectMessage(root, props) {
    if (root.meta.response.topic !== undefined && root.meta.response.topic.messages !== undefined) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]     ");

        setLocalTopicMessages(props.state.generalStates.SESSION_USER_INFO.id, root.meta.response.topic);

        dispatch.InsertMessagesByTopicId(props, root.meta.response.topic)
    } else {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV]     ");
    }
}

export const ParserMeta = (props, root) => {
    if (root.meta.request === undefined) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] request  ");
        return
    }

    if (root.meta.request.what === REQUEST_WHAT_TOPIC && root.meta.request.how === REQUEST_HOW_SELECT) {
        MetaSelectTopic(root, props);
    } else if (root.meta.request.what === REQUEST_WHAT_USER && root.meta.request.how === REQUEST_HOW_SELECT) {
        MetaSelectUser(root, props);
    } else if (root.meta.request.what === REQUEST_WHAT_MESSAGE && root.meta.request.how === REQUEST_HOW_SELECT) {
        MetaSelectMessage(root, props);
    } else {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] META RESPONSE  ");
    }
}