import * as dispatch from "../../dispatchs";
import {
    TYPE_LOGGING_STATUS,
    TYPE_LOGGING_RAW,
    REQUEST_WHAT_REPLY,
    REQUEST_HOW_CREATE,
    REQUEST_WHAT_MENTION
} from "../../../globals";
import { replyReceiveAck } from "../../../../utils/nanoit/notify";

export const ParserNotify = (props, root) => {
    if (root.notify.request === undefined) {
        dispatch.Logger(props, TYPE_LOGGING_STATUS, "[RECV] request  ");
        return
    }

    //  reply   user notify_reply 
    if (root.notify.request.what === REQUEST_WHAT_REPLY && root.from?.me === false ) {

        // REPLY store 
        dispatch.addReplyMessage(props,
            root.notify.response.notify.id,
            root.notify.request.topic.id,
            root.from.user.id,
            root.from.user.auth.name,
            root.notify.request.sequence_id,
        )

        dispatch.Logger(props, TYPE_LOGGING_RAW, "[RECV] " + JSON.stringify(replyReceiveAck(root.notify.request.topic.id)));
        dispatch.Writer(props, replyReceiveAck(root.notify.request.topic.id))

    }else if( root.notify.request.what === REQUEST_WHAT_MENTION && root.from?.me === false ){
        // MENTION store 
        dispatch.addMentionMessage(props,
            root.notify.response.notify.id,
            root.notify.request.topic.id,
            root.from.user.id,
            root.from.user.auth.name,
            root.notify.request.sequence_id,
        )
    }
}