/**
 * @name setLocalAddFriendsAll
 * @summary    
 * @param {*} users
 */
export const setLocalAddFriendsAll = (users) => {
    console.log('setLocalAddFriendsAll')
    setLocalItem('NANOIT_CHAT_FRIENDS_ALL', users)
}
/**
 * @name setLocalAddUserProfile
 * @summary localStorage FRIENDS_ALL  profile 
 * @description       localStorage 
 * @param {*} user 
 * @param {*} imgBlob 
 */
export const setLocalAddUserProfile = (user, imgBlob) => {
    console.log('setLocalAddUserProfile')
    const GET_LOCAL_FRIENDS_ALL = getLocalItem('NANOIT_CHAT_FRIENDS_ALL');
    if( !GET_LOCAL_FRIENDS_ALL ) return;

    const friend = GET_LOCAL_FRIENDS_ALL.find(f => f.id === user.id);

    setLocalItem('NANOIT_CHAT_FRIENDS_ALL', [
        ...(GET_LOCAL_FRIENDS_ALL.filter(f => f.id !== user.id)),
        {
            ...friend,
            profile:{
                ...friend.profile,
                img:imgBlob
            }
        }
    ])
}

/**
 * @name setLocalAddNewUser
 * @summary   
 * @param {*} user
 */
export const setLocalAddNewUser = (user) => {
    if( !user ) return;
    console.log('setLocalAddNewUser')
    const GET_LOCAL_FRIENDS_ALL = getLocalItem('NANOIT_CHAT_FRIENDS_ALL');
    if( !GET_LOCAL_FRIENDS_ALL ) return;

    setLocalItem('NANOIT_CHAT_FRIENDS_ALL', [
        ...(GET_LOCAL_FRIENDS_ALL.filter(f => f.id !== user.id)),
        {
            ...user
        }
    ]);
}

/**
 * @name setLocalUpdateUser
 * @summary  
 * @description      
 * @param {*} user
 */
export const setLocalUpdateUser = (user) => {
    const GET_LOCAL_FRIENDS_ALL = getLocalItem('NANOIT_CHAT_FRIENDS_ALL');
    if( !GET_LOCAL_FRIENDS_ALL ) return;

    const isExistUser = GET_LOCAL_FRIENDS_ALL.find(f => f.id === user.id)

    setLocalItem('NANOIT_CHAT_FRIENDS_ALL', [
        ...(GET_LOCAL_FRIENDS_ALL.filter(f => f.id !== user.id)),
        !!isExistUser
        ? {...(GET_LOCAL_FRIENDS_ALL.find(f => f.id === user.id))}
        : null    
    ])
}


