/**
 * @name makeLocalUserTopicName
 * @summary   
 * @param {*} user_id
 * @param {*} topic_id
 */
export const makeLocalUserTopicName = (user_id, topic_id) => {
    if( !user_id || !topic_id ){
        console.log('makeLocalUserTopicName =>    ')
        return false;
    }
    return `NANOIT_CHAT_USER_${user_id}_${topic_id}`
}

/**
 * @name isLocalUserTopic
 * @summary   
 * @param {*} user_id
 * @param {*} topic_id
 */
export const isLocalUserTopic = (user_id, topic_id) => {
    const isTopic = getLocalItem(makeLocalUserTopicName(user_id, topic_id));
    if( isTopic ){
        return true;
    }else{
        return false;
    }
}


/**
 * @name getLocalUserTopic
 * @summary  '  ' , 
 *            
 * @param {*} user_id
 * @param {*} topic_id
 */
export const getLocalUserTopic = (user_id, topic_id) => {
    if( !user_id || !topic_id ) return;

    const localTopic = makeLocalUserTopicName(user_id, topic_id);
    const getTopic = getLocalItem(localTopic);

    if( !getTopic ) {
        setLocalItem(localTopic, {});
        return getLocalItem(localTopic);
    }else{
        return getTopic;
    }
}

