/**
 * @name setLocalTopicMessages
 * @summary localStorage TOPICS_MY  messages 
 * @description       localStorage 
 * @param {*} topic 
 */
export const setLocalTopicMessages = (user_id, topic) => {
    const GET_LOCAL_USER_TOPIC = getLocalUserTopic(user_id, topic.id);
    if( !GET_LOCAL_USER_TOPIC.messages ) {
        const localTopic = makeLocalUserTopicName(user_id, topic.id);
        setLocalItem(localTopic, topic);
    }
}




