import * as dispatch from "./dispatchs";
import {
    HIDE_LOGIN, 
    HIDE_SIGNUP,
    GET_PROFILE_IMG,
    GET_REPLY_CONTENT,
    IS_SHOW_POPUP_MSG_ITEMS,
    IS_SHOW_CHATBOX_LOADING,
    GET_IMAGE_BLOB,
    GET_TOPIC_USERS,
    GET_ALL_TOPICS,
    IS_SHOW_CHAT_MEBMER,
    IS_SHOW_INVITE,
    IS_SHOW_CHAT_MOBILE,
    IS_SHOW_CREATE_TOPIC,
    IS_SHOW_POPUP_NOTIFY,
    TEMP_SAVE_MENTION_USERS,
    TEMP_SAVE_MSG_FOR_REALY,
    GET_TOAST_MSG,
    SET_REPLY_INFO,
    SET_MENTION_INFO,
    TEMP_SAVE_MSG_FOR_MENTION,
} from '../globals';

export const viewActions = (props) => {
    return {
        selecTopic: (topicId) => {
            dispatch.SelecTopicById(props, topicId);
        },
        getToast: (msg) => {
            dispatch.getToastPopup(props, msg);
        },
        isShowCreateTopic: (trueFalse) => {
            props.dispatch({
                type: IS_SHOW_CREATE_TOPIC,
                isShow: trueFalse,
            })
        },
        isShowChatMobile: (trueFalse) => {
            props.dispatch({
                type: IS_SHOW_CHAT_MOBILE,
                isShow: trueFalse,
            })
        },
        isShowInvite: (trueFalse) => {
            props.dispatch({
                type: IS_SHOW_INVITE,
                isShow: trueFalse,
            })
        },
        isShowChatUser: (trueFalse) => {
            props.dispatch({
                type: IS_SHOW_CHAT_MEBMER,
                isShow: trueFalse,
            })
        },
        getAllTopics: (topicsArr) => {
            props.dispatch({
                type: GET_ALL_TOPICS,
                topicsAll: topicsArr,
            })
        },
        getTopicUsers: (usersArr) => {
            props.dispatch({
                type: GET_TOPIC_USERS,
                topicUsers: usersArr,
            })
        },
        getImageBlob: (sendImage) => {
            props.dispatch({
                type: GET_IMAGE_BLOB,
                sendImage: sendImage,
            })
        },
        isShowChatboxLoading: (trueFalse) => {
            props.dispatch({
                type: IS_SHOW_CHATBOX_LOADING,
                isShow: trueFalse,
            })
        },
        isHideSignup: (trueFalse) => {
            props.dispatch({
                type: HIDE_SIGNUP,
                isShow: trueFalse,
            })
        },
        isHideLogin: (trueFalse) => {
            props.dispatch({
                type: HIDE_LOGIN,
                isShow: trueFalse,
            })
        },
        getProfileImg: (file) => {
            props.dispatch({
                type: GET_PROFILE_IMG,
                file: file,
            })
        },
        getReplyContent: (msg) => {
            props.dispatch({
                type: GET_REPLY_CONTENT,
                msg: msg,
            })
        },
        isShowPopupMessageItems: (trueFalse, item) => {
            props.dispatch({
                type: IS_SHOW_POPUP_MSG_ITEMS,
                isShow: trueFalse,
                item:item,
            })
        },
        isShowPopupNotify: (trueFalse) => {
            props.dispatch({
                type: IS_SHOW_POPUP_NOTIFY,
                isShow: trueFalse,
            })
        },
        
        tempSaveMentionUsers: (user) => {
            props.dispatch({
                type: TEMP_SAVE_MENTION_USERS,
                user: user,
            })
        },
        tempSaveMsgForRelay: (msg) => {
            props.dispatch({
                type: TEMP_SAVE_MSG_FOR_REALY,
                msg: msg,
            })
        },
        tempSaveMsgForMention: (msg) => {
            props.dispatch({
                type: TEMP_SAVE_MSG_FOR_MENTION,
                msg: msg,
            })
        },
        setReplyInfo: (replies) => {
            props.dispatch({
                type: SET_REPLY_INFO,
                replies: replies,
            })
        },
        setMentionInfo: (mentions) => {
            props.dispatch({
                type: SET_MENTION_INFO,
                mentions: mentions,
            })
        },
    }
}