import React, { useState, useEffect, useContext, useCallback } from 'react';
import {StoreContext} from "../../../context/store/storeContext";
import { MetaRequestAllUser } from '../../../utils/nanoit/meta';

function PopupUsers() {
    const {state, actions} = useContext(StoreContext);
    let topic = state.generalStates.TOPICS_MY.filter(e => e.id === state.viewStates.selectopicid)[0];

    /**
     * @summary      ''   
     */
    useEffect(() => {
        topic = state.generalStates.TOPICS_MY.filter(e => e.id === state.viewStates.selectopicid)[0]
    }, [state.generalStates.TOPICS_MY, state.generalStates.FRIENDS_ALL]);


    const Users = useCallback(() => {

        return (
            <ul>
                {
                    topic?.users?.map((user, i) => {

                        //        
                        if( !user ) return;

                        return(
                            <li key={i}>
                                <div className="left">
                                    {
                                        /**
                                         * @summary  user  profile img    
                                         */
                                        user?.profile?.file_id == undefined
                                        ? <img src="images/noimg.jpg" alt="profile noimg" />
                                        : <img src={state.generalStates.FRIENDS_ALL.find(e => e.profile.file_id === user.profile?.file_id).profile.img} />
                                    }
                                    {user?.auth?.name}
                                </div>
                                <div className="right">{topic.owner===user.id && ''}</div>
                            </li>
                        )
                    })
                }
            </ul>
        )
    }, [state.viewStates.isShowChatUser]); //         



    return (
        <div className="popup_right popup_users" isshow={state.viewStates.isShowChatUser==true ? 'active' : ''}>
            <div className="Chatuser_header">
                <button 
                    onClick={() => actions.viewActions.isShowChatUser(false)}
                    className="btn_confirm">
                    
                </button>
                <div className="numbox">
                    <span className="num">
                        {topic?.users?.length}
                    </span>
                    <span> </span>
                </div>
            </div>
            
            <Users />
        </div>
    );
}

export default React.memo(PopupUsers);
