import React, {useContext} from 'react';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import {StoreContext} from "../../../context/store/storeContext";
import SpeakerNotesIcon from '@material-ui/icons/SpeakerNotes';
import MeetingRoomIcon from '@material-ui/icons/MeetingRoom';
import {CtrlTopicLeave} from "../../../utils/nanoit/ctrl";


function ChatHeader() {
    const {state, actions} = useContext(StoreContext);

    const clickExitTopic = () => {
        if(window.confirm('  ?')) {
            actions.generalActions.WebsocketSendData(CtrlTopicLeave(state.viewStates.selectopicid))
            actions.viewActions.isShowChatMobile(false); //   UI 
        }
    }


    return (
        <div className="chatHeader">
            <button
                title="  "
                className="btn_closechat"
                onClick={() => actions.viewActions.isShowChatMobile(false)}>
                <ArrowBackIcon />
            </button>


            <button title="  " className="btn_member" onClick={() => actions.viewActions.isShowChatUser(true)}>
                <SpeakerNotesIcon />
            </button>

            <button title=" " className="btn_invite" onClick={() => actions.viewActions.isShowInvite(true)}>
                <PersonAddIcon />
            </button>

            <button title=" " className="btn_exit" onClick={clickExitTopic}>
                <MeetingRoomIcon />
            </button>
        </div>
    );
}

export default React.memo(ChatHeader);
