import * as dispatch from "./dispatchs";
import {REQUEST_HOW_SEND, TRACE_STATUS_SEND} from "../globals";



export const generalActions = (props) => {
    return {
        /**
         * GENERALS
         */
        Logger: (type, log) => {
            dispatch.Logger(props, type, log)
        },

        /**
         * SESSIONS
         */
        UpdateIsWriteLog: (isWriteLog) => {
            dispatch.UpdateIsWriteLog(props, isWriteLog)
        },
        UpdateLogin: (isLogin) => {
            dispatch.UpdateSessionLogin(props, isLogin);
        },
        UpdateJwt: (token) => {
            dispatch.UpdateSessionJwtToken(props, token);
        },
        UpdateUser: (userName, userId) => {
            dispatch.UpdateSessionUser(props, userId, userName);
        },

        /**
         * TOPICS
         */
        InsertMyTopic: (topic) => {
            dispatch.InsertMyTopic(props, topic);
        },
        // TOPIC_CREATE_USING_TOPICS_ARRAY: (topics) => {
        //     dispatch.DISPATCH_TOPIC_CREATE_USING_TOPICS(props, topics);
        // },
        ResetAllStates: () => {
            dispatch.Reset(props);
        },
        DeleteAllTopics: () => {
            dispatch.DropMyTopics(props);
        },
        DeleteTopicByTopicId: (topic_id) => {
            dispatch.DeleteMyTopicByTopicId(props, topic_id);
        },
    
        /**
         * USERS
         */
        InsertUserById: (topic) => {
            dispatch.InsertUserByTopicId(props, topic);
        },
        InsertNewUserById: (topicId, user, delIdx) => {
            dispatch.InsertNewUserByTopicId(props, topicId, user, delIdx);
        },
        InsertUserChange: (topic, user) => {
            dispatch.InsertUserChangeInfo(props, topic, user);
        },
        DeleteUserById: (topic, user) => {
            dispatch.DeleteUserByTopicId(props, topic, user);
        },

        /**
         * MESSAGES
         */
        InsertMessageById: (topic_id, message) => {
            dispatch.InsertMessageByTopicId(props, topic_id, message);
        },
        InsertMessagesById: (topic) => {
            dispatch.InsertMessagesByTopicId(props, topic);
        },
        updateMessageAddFile: (topic, file, sequence_id) => {
            dispatch.updateMessageByAddFile(props, topic, file, sequence_id);
        },

        /**
         * WEBSOCKETS
         */
        WebsocketUpdateStatus: (webSocketStatus) => {
            dispatch.UpdateWebsocketStatus(props, webSocketStatus);
        },
        WebsocketUpdateWriter: (sendMessage) => {
            dispatch.UpdateWriter(sendMessage);
        },
        WebsocketReceiveData: (json) => {
            dispatch.ReceiveData(json, props);
        },
        WebsocketSendData: (json) => {
            dispatch.Writer(props, json)
        },
        WebsocketSendChatMessage: (json) => {
            dispatch.InsertMessageByTopicId(props, json.msg.request.topic.id, {
                uuid: json.msg.uuid,
                status: TRACE_STATUS_SEND,
                message_type: REQUEST_HOW_SEND,
                sequence_id: 0,
                message: json.msg.request.message,
                from: {
                    user: {
                        id: props.state.generalStates.SESSION_USER_INFO.id,
                        name: props.state.generalStates.SESSION_USER_INFO.name,
                    }
                }
            })
            dispatch.WriterChatMessage(props, json)
        },


        /**
         * PROFILE
         */
        UpdateProfile : (userid, fileid) => {
            dispatch.updateProfileId(props, userid, fileid)
        },
        addProfileImg : (fileid, userid, img) => {
            dispatch.addProfileImgfile(props, fileid, userid, img)
        },

        /**
         * NOTIFY => REPLY, MENTION
         */
        AddReply:(notify_id, topic_id, user_id, user_name, sequence_id) => {
            dispatch.addReplyMessage(props, notify_id, topic_id, user_id, user_name, sequence_id)
        },
        AddMention:(notify_id, topic_id, user_id, user_name, sequence_id) => {
            dispatch.addMentionMessage(props, notify_id, topic_id, user_id, user_name, sequence_id)
        },
    }
}