import React, {useContext, useState} from 'react';
import {StoreContext} from "../../../context/store/storeContext";
import AddIcon from '@material-ui/icons/Add';
import SettingsIcon from '@material-ui/icons/Settings';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import {AuthLogoutNormal} from "../../../utils/nanoit/auth";



function Navigation({ isShowNavi, setIsShowNavi }) {
    const {state, actions} = useContext(StoreContext);

    const clickCreateTopic = () => {
        actions.viewActions.isShowCreateTopic(true);
        setIsShowNavi(false);
    }

    const handleLogout = () => {
        if(window.confirm('  ?')) {
            actions.generalActions.WebsocketSendData(AuthLogoutNormal())
            actions.viewActions.isHideLogin(false);
            setIsShowNavi(false);

            //   key 
            localStorage.removeItem('chattingLoginKey')
        }
    }


    return (
        <div className="navigation" isshow={isShowNavi==true ? 'active' : ''}>
            <div className="head"></div>

            <button className="buttonbox" onClick={clickCreateTopic}>
                <AddIcon />
                <span> </span>
            </button>

            {/* <button className="buttonbox">
                <SettingsIcon />
                <span></span>
            </button> */}

            <button className="buttonbox" onClick={handleLogout}>
                <ExitToAppIcon />
                <span></span>
            </button>
        </div>
    );
}

export default React.memo(Navigation);
