/**
 * @name react-toastify
 * @summary toast popup
 * @link https://github.com/fkhadra/react-toastify
 * @description 
 * 1. <ToastContainer />  APP  .
 * 2. toast('')   .
 */

import { ToastContainer, toast } from 'react-toastify';

export const showToast = (msg) => {
    toast(msg, {
        position: "top-center",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
}
