import React, {useContext, useState, useMemo, useRef, useCallback} from 'react';
import {StoreContext} from "../../../context/store/storeContext";
import {AuthLoginIdPw, AuthLoginTokenSelect} from "../../../utils/nanoit/auth";
import Cat from '../Cat';
import sha256 from 'sha256';
import { useForm } from "react-hook-form"; // https://react-hook-form.com/

function Login() {
    const {state, actions} = useContext(StoreContext)
    
    const { register, handleSubmit, watch, formState: { errors } } = useForm();
    const onSubmit = data => {
        if( errors.email && errors.password ){
            actions.viewActions.getToast('  ')
            return;
        }else{
            // actions.viewActions.getToast('  !')
            actions.generalActions.WebsocketSendData(AuthLoginIdPw(data.email, data.password))
            // actions.generalActions.WebsocketSendData(AuthLoginTokenSelect())

            // sha256  
            // console.log(sha256(password))
        }
    }
    // console.log(watch('email'))


    return (
        <div className="login">
            <div className="box">
                <Cat />
                <div className="info">
                    <h2></h2>
                </div>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <input autoComplete="username" type="email"  placeholder="" className="input_common" {...register('email', {required:true})} />
                    <input autoComplete="current-password" type="password" placeholder="" className="input_common" {...register('password', {required:true})} />
                    <button type="submit" value="submit"></button>
                </form>
            </div>
        </div>
    );
}

export default React.memo(Login);
