import React, { useState, useContext, useEffect } from 'react';

import '../../scss/style.scss';

import Header from './layout/Header';
import Wrapper from './layout/Wrapper';
import Contents from './layout/Contents';
import Navigation from './layout/Navigation';
import List from './list/List';
import Chat from './chat/Chat';
import Signup from './member/Signup';
import Login from './member/Login';
import { StoreContext } from '../../context/store/storeContext';
import axios from 'axios';
import { AuthLogoutNormal } from '../../utils/nanoit/auth';

import { MetaRequestTopicMessage, MetaRequestUserTopicId } from '../../utils/nanoit/meta';
import Notify from './notify/Notify';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { showToast } from '../../utils/Toastify';
import { deviceDivider } from '../../utils/deviceDivider';
import { ProfileDownload } from '../../utils/fileUtil';
import { getLocalUserTopic } from '../../context/localStorageActions';

function ViewApp() {
    const {state, actions} = useContext(StoreContext);

    const [isShowNavi, setIsShowNavi] = useState(false);
    //const [hideLogin, setHideLogin] = useState(false);

    /* //      
    useEffect(() => {
        console.log(state.viewStates.popupMessageItems);
    }, [state.viewStates.popupMessageItems]);
    */

    //    
    useEffect(() => {
        deviceDivider();
    }, []);

    /**
     * @summary Notification API (HTML5) window   
     * : https://dororongju.tistory.com/125 [  ]
     */
    useEffect(() => {
        // Notification.requestPermission();
        // new Notification("", {body:' '});

        //  
        /* function getNotificationPermission() {
            //    
            if (!("Notification" in window)) {
                alert("    .");
            }
            //    
            Notification.requestPermission(function (result) {
                //  
                if(result == 'denied') {
                    alert(' .\n     .');
                    return false;
                }
            });
        }
        getNotificationPermission();

        // new Notification("", {body:''});

        //  
        function notify(msg) {
            var options = {
                body: msg
            }
            
            //   
            var notification = new Notification(" ", options);
            
            // 3  
            setTimeout(function(){
                notification.close();
            }, 3000);
        }
        notify('  !!'); */
    }, []);

    /*********************************************************************
     *    (  token  )
     *********************************************************************/
    useEffect(() => {
        const localstorage_loginkey = localStorage.getItem('chattingLoginKey');
        if( !localstorage_loginkey ){
            return
        }else{
            if (state.generalStates.WEBSOCKET_STATUS === 'Open') {
                // localStorage.removeItem('chattingLoginKey'); //         ,  

                // console.log('  ');
                // actions.generalActions.WebsocketSendData(AuthLoginToken(localstorage_loginkey));
            }
        }
    }, [state.generalStates.WEBSOCKET_STATUS]);


    /**********************************************************************
     * @summary      
     * @description
     * * 1.  UI ,
     * * 2.      ,    
     *********************************************************************/

    useEffect(() => {
/*
        console.log(
            state.viewStates.hideSignup,
            state.generalStates.SESSION_USER_INFO,
            state.generalStates.SESSION_JWT_TOKEN
        )
*/
        if( state.viewStates.hideSignup === true
            && state.generalStates.SESSION_USER_INFO.id !== null
            && state.generalStates.SESSION_JWT_TOKEN !== null
        ) {
            //    
            if( !state.viewStates.profileImg ) return;
            console.log('    ')

            let formData = new FormData();
            formData.append("file", state.viewStates.profileImg);
            
            axios({
                method:'POST',
                url:state.generalStates.DEFAULT_CHAT_SERVER_URL + '/api/v1/upload/profile?user_id=' + state.generalStates.SESSION_USER_INFO.id,
                headers:{
                    'Authorization': 'Bearer ' + state.generalStates.SESSION_JWT_TOKEN
                },
                data:formData
            })
            .then(res => {
                /**
                 * @summary      
                 * @description 
                 * 1. file_id store 
                 * 2.  file_id    
                 */
                actions.generalActions.UpdateProfile(
                    state.generalStates.SESSION_USER_INFO.id,
                    res.data.file.response.file.id
                )
                console.log('    ')
            })
            .catch(e => {
                console.log(' :    ')

                /**
                 * @summary      
                 * @description
                 * 1. 
                 * 2.  
                 */
                // 1. 
                actions.generalActions.WebsocketSendData(AuthLogoutNormal())
                actions.viewActions.isHideLogin(false);
                //   key 
                localStorage.removeItem('chattingLoginKey')

                // 2.   (  )
                // actions.generalActions.WebsocketSendData( )
            })
        }
    }, [
        state.viewStates.hideSignup,
        state.generalStates.SESSION_USER_INFO,
        state.generalStates.SESSION_JWT_TOKEN
    ]);


    /***********************************************************************
     * @summary      
     * @description 
     * * 1.      file_id    .
     * * 2.       ( TOPIC_MY.users +=  )
     **********************************************************************/
    useEffect(() => {
        // console.log('  ')

        const newUserIndex = (state.generalStates.FRIENDS_ALL.length)-1;
        const newUser = state.generalStates.FRIENDS_ALL[newUserIndex];

        if( !newUser ) return;

        const callbackGetImg = (img) => {
            actions.generalActions.addProfileImg(
                newUser.profile.file_id, 
                newUser.id, 
                img
            )
        }
        ProfileDownload(
            newUser.profile.file_id, 
            state.generalStates.DEFAULT_CHAT_SERVER_URL, 
            state.generalStates.SESSION_JWT_TOKEN,
            callbackGetImg
        );

        const topic = state.generalStates.TOPICS_MY.find(t => t.id === state.viewStates.selectopicid);

        //   undefined ...
        if( topic !== undefined && topic.users[topic.users.length-1] === undefined ){
            actions.generalActions.InsertNewUserById(topic.id, newUser, topic.users.length-1);
        }
    }, [state.generalStates.FRIENDS_ALL.length]); //   

    /*************************************************************************
     *    
     ************************************************************************/
    useEffect(() => {
        if( !!state.viewStates.selectopicid ){
            actions.viewActions.isShowChatboxLoading(false);
            setTimeout(function(){
                actions.viewActions.isShowChatboxLoading(true);
            },10);

            actions.generalActions.WebsocketSendData(MetaRequestUserTopicId(state.viewStates.selectopicid)); //   

            /**
             * 1.    ,
             * 2.   messages ,  sequence_id     
             */
            const GET_LOCAL_USER_TOPIC = getLocalUserTopic(
                state.generalStates.SESSION_USER_INFO.id,
                state.viewStates.selectopicid
            );

            if( !GET_LOCAL_USER_TOPIC.messages ){
                actions.generalActions.WebsocketSendData(MetaRequestTopicMessage(state.viewStates.selectopicid));
                console.log('  ');
            }else{
                // actions.generalActions.WebsocketSendData(MetaRequestTopicMessage(state.viewStates.selectopicid)); //   .
                actions.generalActions.InsertMessagesById(GET_LOCAL_USER_TOPIC)
                console.log('  ');
            }
        }
    }, [state.viewStates.selectopicid]);


    //  reply, mention 
    useEffect(() => {
        // const GET_LOCAL_NANOIT = getLocalItem('NANOIT');
        // const user = GET_LOCAL_NANOIT?.CHAT?.USERS.find(u => u.id === state.generalStates.SESSION_USER_INFO.id);
        // const topic = user?.TOPICS_MY?.find(t => t.id === state.viewStates.selectopicid);
        const myId = state.generalStates.SESSION_USER_INFO.id;
        let replySet = new Set();
        let mentionSet = new Set();

        /* if( !topic?.messages ){
            // console.log('  ');
        }else{
            // console.log('  ');
            topic?.messages?.map((msg, i) => {
                if (msg.custom?.notify.reply &&  //   
                    msg.custom?.notify.reply.to_user_id === myId){
                    // console.log('   ');
                    replySet.add(msg);
                }
                if( msg.custom?.notify.mention && //  
                    msg.custom?.notify.mention.to_users_id.find(u => u.id === myId)){ 
                    // console.log('  ');
                    mentionSet.add(msg)
                }else{
                    console.log('')
                }
            })
        } */


        if( replySet.size > 0 ){
            const replies = [...replySet];
            actions.viewActions.setReplyInfo(replies);
        }else if( mentionSet.size > 0 ){
            const mentions = [...mentionSet];
            actions.viewActions.setMentionInfo(mentions);
        }
    }, [state.viewStates.selectopicid, state.generalStates.TOPICS_MY]);


    //   
    useEffect(() => {
        showToast(state.viewStates.getToastMsg);
        actions.viewActions.getToast(null);
    }, [state.viewStates.getToastMsg!=null]);


    return (
        <div className="ViewApp">
            <Wrapper>
                {   // X, X   show
                    ( !state.viewStates.hideSignup && !state.viewStates.hideLogin ) && <Signup />
                }

                {!state.viewStates.hideLogin && <Login />}

                <Header isShowNavi={isShowNavi} setIsShowNavi={setIsShowNavi} />
                <Navigation isShowNavi={isShowNavi} setIsShowNavi={setIsShowNavi} />
                <Contents>
                    <List />
                    <Chat />
                </Contents>

                {   //  
                    ( !!state.viewStates.hideSignup && !!state.viewStates.hideLogin ) && <Notify />
                }

            <ToastContainer />
            </Wrapper>
        </div>
    );
}

export default React.memo(ViewApp);
